<template>
<div>

  <!-- 初始化(成員)開始 -->
    <div v-if="this.project_role=='project-member'&&!this.doneAuth">
      此專案尚未進行鏡像倉庫帳戶初始化，請聯絡專案管理員或集群管理員進行相關操作。
    </div>
  <!-- 初始化(成員)結束 -->

  <!-- 初始化(管理員)開始 -->
    <div v-if="firstAuth">
      此專案尚未進行鏡像倉庫帳戶初始化，請輸入下方資訊進行開通程序。<br>
      一個專案僅能綁定一個鏡像倉庫帳戶。
      <form ref="form" class="mt-5">
        <fieldset>
            <div class="form-group row align-items-end">
              <label class="col-2 col-form-label">用戶名:</label>
              <div class="col-5">
                  <b-form-input v-model="registries.username" :class="{'form-control':true}" type="text"/>
              </div>
            </div>
        </fieldset>      
        <fieldset>
            <div class="form-group row align-items-start">
              <label class="col-2 col-form-label">密碼:</label>
              <div class="col-5">
                <b-form-input v-model="registries.password" :class="{'form-control':true}" type="text"/>
                <span class="must_write">密碼至少8位，且至少包含1個小寫字母、1個大寫字母與1個數字。</span>
              </div>
            </div>
        </fieldset>
        <fieldset>
            <div class="form-group row align-items-end">
              <label class="col-2 col-form-label">確認密碼:</label>
              <div class="col-5">
                <b-form-input v-model="registries.repassword" :class="{'form-control':true}" type="text"/>
              </div>
            </div>
        </fieldset>
        <h5 class="py-3">建立帳戶同時會生成倉庫專案 {{this.$route.query.id}}，請選擇設定為公開專案或私人專案</h5>

        <div class="col-12 my-3">
          <label class="switch d-inline-flex">
            <input type="radio" v-model="openArea" name="radioSwitch" value="1" @change="upconfirm()"/>
            <span></span>
          </label>
          <span>公開專案</span><span class="must_write">僅推送時需要帳戶權限</span>
        </div>
        <div class="col-12 my-3">
          <label class="switch d-inline-flex">
            <input type="radio" v-model="openArea" name="radioSwitch" value="2" @change="upconfirm()"/>
            <span></span>
          </label>
          <span>私人專案</span><span class="must_write">推送與拉取皆需要帳戶權限</span>
        </div>
      </form> 
      <b-btn type="button" class="btn btn-primary mr-1 mb-1 mt-2" variant="primary" @click="handleSubmit()">建立帳戶並綁定</b-btn>
    </div>
  <!-- 初始化(管理員)結束 -->

  <!-- 初始化已完成開始 -->
    <div v-if="doneAuth">
      <form ref="form" class="mt-5">

        <fieldset>
            <div class="form-group row align-items-center">
              <h5 class="col-2">綁定用戶名:</h5>
              <div class="col-3">
                 {{username}}
              </div>
              <b-btn variant="outline-danger" class="btn-xs shadow-none" @click.prevent="checkCancel()" v-if="this.project_role=='project-owner'"><i class="fas fa-trash-alt" v-b-tooltip.hover title="刪除用戶並解除綁定"></i></b-btn>
            </div>
        </fieldset>

        <h5 class="my-2">推送鏡像範例 - </h5>

        <ul>
          <li>docker login {{harbor_address}}</li>
          <li>docker tag alpine:latest {{harbor_address}}/{{strProject}}/alpine:latest</li>
          <li>docker push {{harbor_address}}/{{strProject}}/alpine:latest</li>
        </ul>

        <!-- <fieldset>
          <div>docker login {{harbor_address}}</div>
        </fieldset> 

        <fieldset>
          <div>docker tag alpine:latest {{harbor_address}}/{{strProject}}/alpine:latest </div>
        </fieldset> 

        <fieldset>
          <div>docker push {{harbor_address}}/{{strProject}}/alpine:latest</div>
        </fieldset>  -->

      </form>
    </div>
  <!-- 初始化(管理員)結束 -->

  
</div>
</template>  

<script>
import Vue from 'vue';
import swal from 'sweetalert2'

export default {
  props:["strProject","harbor_address"],
  components: {
  },
  data(){
    return{
      project_role:'',
      username:'',
      openArea:'1',
      create_project:'',
      registries: {
        address: "",
        username: "",
        password: "",
        repassword: ""
      },

      firstAuth:false,
      doneAuth:false,
      isLoading: false,

    };
  },
  mounted(){
    this.$emit('stopLoadTable')
    
    backendapi.get(`/project/${this.$route.query.id}/registry`).then((response) => {
      this.$parent.harbor_address = response.data.harbor_address
      this.project_role = response.data.project_role
      if(this.project_role=="project-owner" && response.data.credentail_list.length <= 0){
        this.firstAuth = true
      }else{
        this.firstAuth = false
      }

      if(response.data.credentail_list.length >= 1){
        const b = response.data.credentail_list.map(item => {
          for(let i in item["registries"]){
            return item["registries"][i]
          }
        })  
        this.username = b[0].username 
        this.doneAuth = true
    
      }else{
        this.doneAuth = false
      }
    
    })
    .catch(error => {
      this.isLoading = false;
      alert(error.message)
    });
  },
  watch:{
    $route (to, from){
      this.$emit('stopLoadTable')
      backendapi.get(`/project/${this.$route.query.id}/registry`).then((response) => {
        this.$parent.harbor_address = response.data.harbor_address
        this.project_role = response.data.project_role
        if(this.project_role=="project-owner"&&response.data.credentail_list.length <= 0){
          this.firstAuth = true
        }else{
          this.firstAuth = false
        }
        
        if(response.data.credentail_list.length >= 1){
          const b = response.data.credentail_list.map(item => {
            for(let i in item["registries"]){
              return item["registries"][i]
            }
          })  
          this.username = b[0].username 
          this.doneAuth = true
      
        }else{
          this.doneAuth = false;
        }

      })
      .catch(error => {
        this.isLoading = false;
        alert(error.message)
      });
    }
  },
  methods:{

    upconfirm(){
      if(this.openArea == 1){
        this.create_project = "public"
      }else if(this.openArea == 2){
        this.create_project = "private"
      }
    },

    handleSubmit(){
      var text = /[a-z]/.test(this.registries.password)&&/[A-Z]/.test(this.registries.password)&&/\d/.test(this.registries.password)

      if(this.registries.username == ''){
        alert("請輸入用戶名");
      }else if (this.registries.password.length < 8){
        alert("密碼不可低於8位數");
      }else if ( !text ){
        alert("密碼格式不正確");
      }else if (this.registries.password!==this.registries.repassword){
        alert("密碼不一致");
      }else{
        this.go()
      }

    },
    go(){
      backendapi.post(`/project/${this.strProject}/registry`,{
        create_project: this.create_project,
        registries: {
          address: this.harbor_address,
          username: this.registries.username,
          password: this.registries.password
        }
      }).then((response) => {
        this.$emit('loadTable')
        this.reload()
      })
      .catch(error => {
        alert(error.message);
        this.$emit('loadTable')
      });
    },
    cancel(){
      this.$emit('loadTable')
    },
    reload(){
      backendapi.get(`/project/${this.$route.query.id}/registry`).then((response) => {
        this.project_role = response.data.project_role
        if(this.project_role=="project-owner" && response.data.credentail_list.length <= 0){
          this.firstAuth = true
        }else{
          this.firstAuth = false
        }
        if(response.data.credentail_list.length >= 1){
          const b = response.data.credentail_list.map(item => {
            for(let i in item["registries"]){
              return item["registries"][i]
            }
          })  
          this.username = b[0].username 
          this.doneAuth = true
        }else{
          this.doneAuth = false
        }
      })
      .catch(error => {
        this.isLoading = false;
        alert(error.message)
      });
    },
    // 刪除 //
    checkCancel() { 
      swal({
          title: "是否確定要刪除用戶: " + this.username,
          text: "如服務中使用到私人鏡像，此動作會影響到拉取權限，可能導致服務部屬失敗。",
          type: "warning",
          showCancelButton: true,
          confirmButtonColor: "#DD6B55",
          confirmButtonText: this.$t('common.BTN_CONFIRM'),
          cancelButtonText: this.$t('common.BTN_CANCEL'),
          heightAuto: false
      }).then((result) => {
          if(result.value){
            backendapi.delete(`/project/${this.strProject}/registry/${this.username}`).then((response) => {
                swal({
                    title: this.$t('common.MSG_DEL_SUCCESS'),
                    confirmButtonText: this.$t('common.BTN_OK'),
                    type: "success",
                    heightAuto: false
                });
                this.reload()
              })
              .catch(error => {
                alert(error.message);
              });
          } 
      });
    },
  },
}
</script>

<style>
#createImages .row{
  margin:0 15px;
}
fieldset:last-child{
  margin-bottom: 0;
}
</style>


