<template>
<div>

  <!-- 讀取畫面 -->
  <loading :active.sync="isLoading"></loading>

  <div class="content-heading">{{$t("sidebar.CONFIG_MAP")}}</div>

  <!-- select選項開始 -->
  <b-form class="was-validated">
    <div class="form-group row">
      <label class="col-md-2 col-form-label text-md-right">{{$t("service.SVCM_LBL_PRJ")}}:</label>
      <div class="col-md-4">
        <select id="projectlist" class="custom-select custom-select-md mb-3" @change="selectedproject()" v-model="strProject">
          <option :value="$t('option.PROJECT')" disabled selected>{{$t("option.PROJECT")}}</option>
          <option v-for="(item) in projectlist" :value="item.name" :key="item.name">{{item.name}}</option>
        </select>
      </div>
      <label class="col-md-2 col-form-label text-md-right">{{$t("service.SVCM_LBL_NAMESPACE")}}:</label>
      <div class="col-md-4">
        <select id="namespacelist" class="custom-select custom-select-md mb-3" @change="selectednamespace()" :disabled="disabled" v-model="strNamespace">
          <option id="myselected" :value="$t('option.NAMESPACE')" disabled selected>{{$t("option.NAMESPACE")}}</option>
          <option v-for="item in namespacelist" :value="item.name" :key="item.name">{{item.name}}</option>
        </select>
      </div>
    </div>
  </b-form>
  <!-- select選項結束 -->
    
  <router-view v-show="!this.$route.query.namespace" :strProject="strProject" @getPjname="getPjname" @getNamespace="getNamespace"></router-view>

  <!-- 新建配置開始 -->
  <b-btn type="button" class="btn btn-primary mr-1 mb-1" variant="primary" v-b-modal.createConfigure v-show="this.$route.query.namespace">{{$t("service.CFGMAP_BTN_NEW_CFGMAP")}}</b-btn>
  <br>
  <br>
  <b-modal class="modal fade configure" id="createConfigure" size="xl" :title="$t('service.CFGMAP_DIAL_NEWCFGMAP_TITLE')" ref="modal" :hide-footer="true">
    <NewConfigMap :strProject="strProject" :strNamespace="strNamespace" @loadTable="loadTable()" @stopLoadTable="stopLoadTable()"/>
  </b-modal>
  <!-- 新建配置結束 -->

  <!-- 編輯配置開始 -->
  <b-modal class="modal fade configure" id="editConfigure" size="xl" :title="$t('service.CFGMAP_DIAL_UPD_CFGMAP_TITLE')" ref="modal" :hide-footer="true">
    <EditConfigMap :strProject="strProject" :strNamespace="strNamespace" :strConfig="strConfig" @loadTable="loadTable()" @stopLoadTable="stopLoadTable()"/>
  </b-modal>
  <!-- 編輯配置結束 -->

  <!-- 分類表單開始 -->
  <v-client-table :data="tableData" :columns="columns" :options="options" v-show="this.$route.query.namespace">
    <template slot="action" slot-scope="props">
      <div>
        <b-btn variant="outline-info" class="btn-xs" @click.prevent="editpop(props.row)" v-b-modal.editConfigure v-b-tooltip.hover :title="$t('common.ICON_TIP_EDIT')"><i class="fas fa-edit"></i></b-btn>
        <b-btn variant="outline-danger" class="btn-xs" @click.prevent="checkCancel(props.row)"><i class="fas fa-trash-alt" :title="$t('common.ICON_TIP_DEL')"></i></b-btn>
      </div>
    </template>
  </v-client-table>
  <!-- 分類表單結束 -->

</div>
</template>

<script>
import Vue from 'vue';
import { ClientTable,  Event } from 'vue-tables-2';
import swal from 'sweetalert2'
import NewConfigMap from "./NewConfigMap";
import EditConfigMap from "./EditConfigMap";

Vue.use(ClientTable);

export default {
  components: {
    ClientTable,
    NewConfigMap,
    EditConfigMap
  },
  data(){
    return{   
      url:'configmap',
      timer1:'',
      selectedIndex: '',
      projectlist:[],
      strProject:'',
      namespacelist:[],
      strNamespace:'',
      strConfig:'',
      // showProject:true,
      // showNamespace: false,
      // showConfig: false,
      disabled: true,

      tableData: [],
      columns: ['name', 'description', 'createdTS', 'action'],
      options: {
        sortable: ['name', 'description', 'createdTS'],
        filterable: true,
        texts: {
          filter: this.$t('common.LBL_SEARCH'),
          filterPlaceholder:this.$t('common.IPT_HINT_ENTER_KEY_WORD'),
          count:this.$t('common.LBL_TOTAL') + "Showing {from} to {to} of {count} records|{count}" + this.$t('common.LBL_RECORDS'),
          noResults:this.$t('common.LBL_NO_RECORD'),
        },
        headings: {
            name: () => this.$t("service.CFGMAP_LST_CFGMAP_NAME"),
            description: () => this.$t("service.CFGMAP_LST_DESC"),
            createdTS: () => this.$t("service.CFGMAP_LST_CRTE_TIME"),
            action: () => this.$t("service.CFGMAP_LST_OPT"),
        },
        pagination: { chunk: 5 },
        sortIcon: {
            is: 'fa-sort',
            base: 'fas',
            up: 'fa-sort-up',
            down: 'fa-sort-down'
        },
        dateColumns:['createdTS'],
        dateFormat: 'YYYY-MM-DD HH:mm:ss',
        orderBy:{
          column:"createdTS",
          ascending:false
        }, 
      },
      isLoading: false,
    };
  },
  validators: {
    editnewdata: function(value) {
      console.log('aaaaaaaaaaaa');
    }
  },
  mounted(){
    this.strProject = this.$t('option.PROJECT')
    this.strNamespace = this.$t('option.NAMESPACE')
    this.$router.push({
      path:'/admin/configmap/project',
      params:{
        project:"project"
      }
    })
    this.isLoading = true;
    backendapi.get("/project").then((response) => {
      this.isLoading = false;
      this.projectlist = response.data;
    })
    .catch(error => {
      alert(error.message);
      this.isLoading = false;
    });

  },

  watch:{
    $route (to, from){
      if(location.pathname=="/admin/configmap"){
          this.$router.push({
            path:'/admin/configmap/project',
            params:{
              project:"project"
            }
          })
      }
      if(this.$route.query.project){
        this.strProject = this.$route.query.project
      }else{
        this.strProject = this.$t('option.PROJECT')
      }

      if(this.$route.query.namespace){
        this.strNamespace = this.$route.query.namespace
        backendapi.get(`/project/${this.strProject}/namespace/${this.strNamespace}/configmap`)
          .then(response => {
            this.tableData = response.data.map((item, index) => {
                item['id'] = index;
                item['createdTS'] = moment(item.createdTS);
                return item
            });
            this.disabled = false;
          })
          .catch(error => {
            console.log(error);
            this.isLoading = false;
          });
      }else{
        this.strNamespace = this.$t('option.NAMESPACE')
        this.disabled = true;
      }
    }
  },

  methods:{
    // 選取專案 //
    selectedproject() {
      var projectlist = document.getElementById("projectlist");
      this.strProject = projectlist.options[projectlist.selectedIndex].value;
      clearInterval(this.timer1);

      this.$router.push({
        path:`/admin/${this.url}/namespace`,
        query:{
          project: this.strProject
        }
      })

      this.isLoading = true;
      backendapi.get(`/project/${this.strProject}/namespace`).then(response => {
        this.namespacelist = response.data;
        this.isLoading = false;
        if (this.namespacelist.length >= 1) {
          this.disabled = false;
        } else {
          this.disabled = true;
        }
      })
      .catch(error => {
        alert(error.message);
        this.isLoading = false
      });

    },

    // 選取命名空間 //
    selectednamespace() {
      var namespacelist = document.getElementById("namespacelist");
      this.strNamespace = namespacelist.options[namespacelist.selectedIndex].value;
      clearInterval(this.timer1);

      this.$router.push({
        path:'namespace',
        query:{
          project:this.$route.query.project,
          namespace: this.strNamespace
        }
      })

      backendapi.get(`/project/${this.strProject}/namespace/${this.strNamespace}/configmap`).then((response) => {
        this.tableData = response.data.map((item, index) => {
            item['id'] = index;
            item['createdTS'] = moment(item.createdTS);
            return item
        });
      })
      .catch(error => {
        alert(error.message);
        this.isLoading = false;
      });
    },

    // 接收專案 //
    getPjname(PJname){
      this.strProject = PJname
      this.isLoading = true;
      clearInterval(this.timer1);
      backendapi.get(`/project/${this.strProject}/namespace`).then(response => {
        this.namespacelist = response.data;
        this.isLoading = false
        if (this.namespacelist.length >= 1) {
          this.disabled = false;
        } else {
          this.disabled = true;
        }
      })
      .catch(error => {
        console.log(error);
      });
    },

    // 接收命名空間//
    getNamespace(namespace){
      this.isLoading = true;
      this.strNamespace = namespace
      clearInterval(this.timer1);
      backendapi.get(`/project/${this.strProject}/namespace/${this.strNamespace}/configmap`).then((response) => {
        this.tableData = response.data.map((item, index) => {
            item['id'] = index;
            item['createdTS'] = moment(item.createdTS);
            return item
        });
        this.isLoading = false;
      })
      .catch(error => {
        alert(error.message);
        this.isLoading = false;
      });
    },

    // 元件呼叫讀取表單 //
    loadTable() {
      this.$nextTick(() => {
        this.$refs.modal.hide();
      });
      this.timer1 = setInterval(()=> {this.reloadd()}, 5000);
    },

    // 元件呼叫停止讀取表單 //
    stopLoadTable(){
      clearInterval(this.timer1);
    },

    // 讀取表單 //
    reloadd(){
      backendapi.get(`/project/${this.strProject}/namespace/${this.strNamespace}/configmap`).then((response) => {
        this.tableData = response.data.map((item, index) => {
            item['id'] = index;
            item['createdTS'] = moment(item.createdTS);
            return item
        });
        this.isLoading = false;
      })
    },

    // 編輯環境配置 //
    editpop(row) {
      this.strConfig = row.name
    },

    // 分類表單刪除 //
    remove (row) {
      this.isLoading = true;
      this.tableData.filter(item => (row.id !== item.id));
      this.strConfig = this.tableData[row.id].name;
      backendapi.delete(`/project/${this.strProject}/namespace/${this.strNamespace}/configmap/${this.strConfig}`).then((response) => {
        this.timer1 = setInterval(()=> {this.reloadd()}, 5000);
        this.isLoading = false;
      })
      .catch(error => {
        alert(error.message);
        this.isLoading = false;
      });
    },
    
    // 刪除步驟 //
    checkCancel(row) { 
      swal({
          title: this.$t('service.CFGMAP_DIAL_CFGMAPDEL_MSG'),
          text: this.$t('common.MSG_IRREVERSIBLE_ACTION'),
          type: "warning",
          showCancelButton: true,
          confirmButtonColor: "#DD6B55",
          confirmButtonText: this.$t('common.BTN_CONFIRM'),
          cancelButtonText: this.$t('common.BTN_CANCEL'),
          heightAuto: false
      }).then((result) => {
          if(result.value){
            this.remove(row);
            swal({
                title: this.$t('common.MSG_DEL_SUCCESS'),
                confirmButtonText: this.$t('common.BTN_OK'),
                type: "success",
                heightAuto: false
            });
          } 
      });
    },
  },

 destroyed: function() {
    console.log('configure timer destroyed');
    clearInterval(this.timer1);
  }
}
</script>

<style>
#createConfigure .modal-xl {
  max-width: 900px;
}

#createConfigure .help {
    display: block;
    font-size: 0.75rem;
    margin-top: 0.25rem;
}

#createConfigure .help.is-danger{
    color: #ff3860;
}
#configure  fieldset:last-child{
  margin-bottom: 0;
}
</style>


