<template>
  <div>
    <!-- 讀取畫面 -->
    <loading :active.sync="isLoading"></loading>

    <div class="content-heading">{{$t("sidebar.SVC_MEM")}}</div>

    <!-- select選項開始 -->
    <b-form class="was-validated">
      <div class="form-group row">
        <label class="col-md-2 col-form-label text-md-right">{{$t("service.SVCM_LBL_PRJ")}}:</label>
        <div class="col-md-4">
          <select id="projectlist" class="custom-select custom-select-md mb-3" @change="selectedproject()" v-model="strProject">
            <option :value="$t('option.PROJECT')" disabled selected>{{$t("option.PROJECT")}}</option>
            <option v-for="(item) in projectlist" :value="item.name" :key="item.name">{{item.name}}</option>
          </select>
        </div>
        <label class="col-md-2 col-form-label text-md-right">{{$t("service.SVCM_LBL_NAMESPACE")}}:</label>
        <div class="col-md-4">
          <select id="namespacelist" class="custom-select custom-select-md mb-3" @change="selectednamespace()" v-model="strNamespace" :disabled="disabled">
            <option id="myselected" :value="$t('option.NAMESPACE')" disabled selected>{{$t("option.NAMESPACE")}}</option>
            <option v-for="item in namespacelist" :value="item.name" :key="item.name">{{item.name}}</option>
          </select>
        </div>
      </div>
    </b-form>
    <!-- select選項結束 -->

    <router-view v-show="!this.$route.query.namespace" :strProject="strProject" @getPjname="getPjname" @getNamespace="getNamespace"></router-view>
  
    <!-- 新建按鈕開始 -->
    <b-btn type="button" class="btn btn-primary mr-1 mb-1" variant="primary" v-b-modal.createService v-show="this.$route.query.namespace">{{$t("service.SVCM_BTN_BTN_NEWSVC")}}</b-btn>
    <!-- <a class="mb-1 btn btn-primary" v-show="showService" :href="'https://10.62.164.163:8087/kiali/console/graph/namespaces/?edges=requestsPerSecond&graphType=versionedApp&namespaces='+this.strNamespace+'&unusedNodes=true&injectServiceNodes=false&pi=15000&duration=60&layout=dagre?kiosk=true'" target="blank" style="padding: .375rem 1rem;">{{$t('common.ICON_TIP_TRACE')}}</a> -->
    <a class="mb-1 btn btn-primary" href="" style="padding: .375rem 1rem;" v-b-modal.link @click.prevent v-show="this.$route.query.namespace">{{$t('common.ICON_TIP_TRACE')}}</a> 
    <br />
    <br />
    <!-- 新建按鈕結束 -->

    <!-- 服務的調用練追蹤popup開始 -->
    <b-modal class="modal fade" id="link" size="xl" :title="$t('service.SVCM_DIAL_TRACEING_TITLE')" ref="modal" :hide-footer="true">
      <embed width="100%" height="650" :src="this.reversProxyUrl + this.clusterid + '/kiali/console/graph/namespaces/?edges=requestsPerSecond&graphType=versionedApp&namespaces=' + this.strNamespace + '&unusedNodes=true&injectServiceNodes=false&pi=15000&duration=60&layout=dagre&kiosk=true'" frameborder="0" allowfullscreen></embed> 
    </b-modal>
    <!-- 服務的調用練追蹤popup結束 -->
    
    <!-- 新建服務開始 -->
    <b-modal class="modal fade" id="createService" size="xl" :title="$t('service.SVCM_DIAL_NEWSVC_BASICINFO_TITLE')" ref="modal" :hide-footer="true">
      <NewService :strProject="strProject" :strNamespace="strNamespace" v-on:childMethod="parentMethod"/>
    </b-modal>
    <!-- 新建服務結束 -->

    <!-- 服務表單開始 -->
    <v-client-table :data="tableData2" :columns="columns2" :options="options2" v-show="this.$route.query.namespace">
      <template slot="state" slot-scope="props">
        <div>
          <div class="p-2" :class="[unactive, props.row.state=='active'? active : '']">
            {{props.row.state}}
          </div>  
        </div>
      </template>
      <template slot="action" slot-scope="props">
        <div>
          <b-btn variant="outline-info" class="btn-xs" v-if="props.row.state=='active'" @click.prevent="viewlog(props.row)" v-b-modal.containerLog v-b-tooltip.hover :title="$t('service.SVCM_DIAL_CONTAINER_LOG_TITLE')">
            <em class="fa-x fas fa-eye"></em>
          </b-btn>
          <b-btn variant="outline-info" class="btn-xs" v-if="props.row.state=='active'" @click.prevent="rollback(props.row)" v-b-modal.createRollback v-b-tooltip.hover :title="$t('service.SVCM_DIAL_SVCRBK_TITLE')">
            <!--i class="fas fa-redo-alt"></i-->
            <i class="fas fa-history"></i>
          </b-btn>
          <!-- <b-btn variant="outline-success" class="btn-xs" v-if="props.row.state=='active'" @click.prevent="authorize(props.row)" v-b-modal.createAuthorize v-b-tooltip.hover :title="$t('service.SVCM_DIAL_SVCMODIFY_TITLE')">
              <i class="fas fa-draw-polygon"></i>
          </b-btn> -->
          <b-btn variant="outline-info" class="btn-xs" v-if="props.row.state=='active'" @click.prevent="upgrade(props.row)" v-b-modal.createUpgrade v-b-tooltip.hover :title="$t('common.ICON_TIP_SVC_UPGRADE')">
              <!-- i class="fas fa-chevron-up"></i -->
              <i class="fas fa-arrow-circle-up"></i>
          </b-btn>

          <b-btn variant="outline-success" class="btn-xs" v-if="props.row.state=='active'" @click.prevent="Info(props.row)" v-b-modal.createInfo v-b-tooltip.hover :title="$t('common.ICON_TIP_SVC_INFO')">
              <i class="fas fa-info-circle"></i>
          </b-btn>

          <b-btn variant="outline-info" class="btn-xs" v-if="props.row.state=='active'" @click.prevent="Clone(props.row)" v-b-modal.createClone v-b-tooltip.hover :title="$t('common.ICON_TIP_SVC_CLONE')">
              <i class="far fa-clone"></i>
          </b-btn>

          <b-btn variant="outline-danger" class="btn-xs" v-if="props.row.state!=='removing'" @click.prevent="checkCancel2(props.row)" v-b-tooltip.hover :title="$t('common.ICON_TIP_DEL')">
              <i class="fas fa-trash-alt"></i>
          </b-btn>
        </div>
      </template>
    </v-client-table>
    <!-- 服務表單結束 -->

    <!-- 服務返回開始 -->
    <b-modal class="modal fade" id="createRollback" :title="$t('service.SVCM_DIAL_SVCRBK_TITLE')" ref="modal" :hide-footer="true" >
        <RollbackService :strProject="strProject" :strNamespace="strNamespace" :strservicename="strservicename" v-on:childMethod="parentMethod" />
    </b-modal>
    <!-- 服務返回結束 -->

    <!-- 服務鑑權開始 -->
    <!-- <b-modal class="modal fade" id="createAuthorize" size="xl" :title="$t('service.SVCM_DIAL_SVCMODIFY_TITLE')" ref="modal" :hide-footer="true">
        <Authorize :strProject="strProject" :strNamespace="strNamespace" :strservicename="strservicename" v-on:childMethod="parentMethod" />
    </b-modal> -->
    <!-- 服務鑑權結束 -->

    <!-- 升級服務開始 -->
    <b-modal class="modal fade" id="createUpgrade" size="xl" :title="$t('service.SVCM_DIAL_SVCUPGRADE_TITLE')" ref="modal" :hide-footer="true">
        <Upgrade :strProject="strProject" :strNamespace="strNamespace" :strservicename="strservicename" v-on:childMethod="parentMethod" />
    </b-modal>
    <!-- 升級服務結束 -->

    <!-- 服務資訊開始 -->
    <b-modal class="modal fade" id="createInfo" size="xl" :title="$t('service.SVCM_DIAL_SVCINFO_TITLE')" ref="modal" :hide-footer="true">
        <EditService :strProject="strProject" :strNamespace="strNamespace" :strservicename="strservicename" v-on:childMethod="parentMethod" />
    </b-modal>
    <!-- 服務資訊結束 -->

    <!-- 服務複製開始 -->
    <b-modal class="modal fade" id="createClone" size="xl" :title="$t('service.SVCM_DIAL_SVCCLONE_TITLE')" ref="modal" :hide-footer="true">
        <CloneService :strProject="strProject" :strNamespace="strNamespace" :strservicename="strservicename" v-on:childMethod="parentMethod" />
    </b-modal>
    <!-- 服務複製結束 -->

    <!-- 容器日誌開始 -->
    <b-modal class="modal fade" id="containerLog" :title="$t('service.SVCM_DIAL_CONTAINER_LOG_TITLE')" ref="modal" size="xl" :hide-footer="true">
        <div class="row">
            <div class="col-lg-3">
                <div class="card mb-3 border-primary">
                    <div class="card-header text-white bg-primary">{{$t("service.SVCM_DIAL_CONTAINER_LOG_OPT_REPLICA")}}</div>
                    <div class="card-body">
                        <div class="d-flex align-items-center flex-nowrap" v-for="replica in replicas" :key="replica.id">
                            <input type="radio" v-model="selectedReplica" name="replicas" :value="replica.name" />
                            <span>{{replica.displayname}}({{replica.created}})</span>
                        </div>
                    </div>
                </div>
                <div class="card mb-3 border-primary">
                    <div class="card-header text-white bg-primary">{{$t("service.SVCM_DIAL_CONTAINER_LOG_OPT_CONTAINER")}}</div>
                    <div class="card-body">
                        <div class="d-flex align-items-center flex-nowrap" v-for="container in containers" :key="container.name">
                            <input type="radio" v-model="selectedContainer" name="containers" :value="container.name" /> 
                            <span>{{container.name}}</span>
                        </div>
                    </div>
                </div>
            </div>
            <div class="col-lg-9">
                <div class="form-group row mx-0 justify-content-start">
                    <div class="col-md-auto">
                        <input class="form-check-input" id="inlineRadio1" type="radio" name="inlineRadioOptions" v-model="viewAllLog" :value="true" />
                        <label class="form-check-label" for="inlineRadio1">{{$t("service.SVCM_DIAL_CONTAINER_LOG_OPT_SHOW_ALL")}}</label>
                    </div>
                    <div class="col-md-auto">
                        <input class="form-check-input" id="inlineRadio2" type="radio" name="inlineRadioOptions" v-model="viewAllLog" :value="false" />
                        <label class="form-check-label" for="inlineRadio2">{{$t("service.SVCM_DIAL_CONTAINER_LOG_OPT_SHOW_LAST")}}</label>
                        <input id="number" type="number" min="0" step="50" v-model="logLineNumber" /> {{$t("service.SVCM_DIAL_CONTAINER_LOG_OPT_RECORDS")}}
                    </div>
                    <div class="col-md-auto">
                        <button class="btn btn-primary btn-xs mr-1" type="button" @click="query()">{{$t("service.SVCM_DIAL_CONTAINER_LOG_BTN_QUERY")}}</button>
                    </div>
                </div>
                <div id="logContent" class="logContent col-12" v-html="logContent">

                </div>
            </div>
        </div>
    </b-modal>
    <!-- 容器日誌結束 -->

  </div>
</template>

<script>
import Vue from "vue";
import { ClientTable, Event } from "vue-tables-2";
import swal from "sweetalert2";
import ProjectTable from "./ProjectTable";
import NewService from "./NewService";
import RollbackService from "./RollbackService";
import Authorize from "./Authorize";
import Upgrade from "./Upgrade";
import EditService from "./EditService";
import CloneService from "./CloneService";

Vue.use(ClientTable);

export default {
  name: "Service",
  components: {
    ClientTable,
    ProjectTable,
    NewService,
    RollbackService,
    Authorize,
    Upgrade,
    EditService,
    CloneService
  },
  data() {
    return {
      reversProxyUrl:'',
      clusterid:'',
      url:'service',
      unactive:'unactive',
      active:'active',
      timer1:'',
      timer2:'',

      logLineNumber: 50,
      selectedReplica: null,
      selectedContainer: null,
      logContent: "",
      viewAllLog: true,
      replicas: [],
      containers: [],

      spacename: "",
      spacenameState: null,
      spacetext: "",
      spacetextState: null,
      selectedIndex: "",
      selectedProject: "",
      projectlist: [],
      strProject: "",
      namespacelist: [],
      strNamespace: "",
      strservicename: "",
      showProject:true,
      showNamespace: false,
      showService: false,
      disabled: true,

      tableData2: [],
      columns2: ["name", "state", "image", "createdTS", "action"],
      options2: {
        sortable: ["name", "state", "image", "createdTS"],
        filterable: true,
        texts: {
          filter: this.$t('common.LBL_SEARCH'),
          filterPlaceholder: this.$t('common.IPT_HINT_ENTER_KEY_WORD'),
          count: this.$t('common.LBL_TOTAL') + "Showing {from} to {to} of {count} records|{count}" + this.$t('common.LBL_RECORDS'),
          noResults: this.$t('common.LBL_NO_RECORD')
        },
        headings: {
          name: () => this.$t('service.SVCM_LST_SVC_NAME'),
          state: () => this.$t('service.SVCM_LST_SVC_STATUS'),
          image: () => this.$t('service.SVCM_LST_CONTAINER_IMG'),
          createdTS: () => this.$t('service.SVCM_LST_CRTE_TIME'),
          action: () => this.$t('service.SVCM_LST_OPT')
        },
        columnsClasses:{
          state: "state",
        },
        pagination: { chunk: 5 },
        sortIcon: {
          is: "fa-sort",
          base: "fas",
          up: "fa-sort-up",
          down: "fa-sort-down"
        },
        dateColumns:['createdTS'],
        dateFormat: 'YYYY-MM-DD HH:mm:ss',
        orderBy:{
          column:"createdTS",
          ascending:false
        }, 
      },
      isLoading: false
    };
  },
  mounted() {
    this.reversProxyUrl = reversProxyUrl
    this.clusterid = localStorage.getItem('clusterid')
    this.strProject = this.$t('option.PROJECT')
    this.strNamespace = this.$t('option.NAMESPACE')
    this.$router.push({
      path:'/admin/service/project',
      params:{
        project:"project"
      }
    })
    this.isLoading = true;
    backendapi.get("/project").then(response => {
        this.isLoading = false;
        this.projectlist = response.data;
      })
      .catch(error => {
        console.log(error);
    });
  },
  watch:{
    $route (to, from){
      if(location.pathname=="/admin/service"){
        this.$router.push({
          path:'/admin/service/project',
          params:{
            project:"project"
          }
        })
      }
      if(this.$route.query.project){
        this.strProject = this.$route.query.project
      }else{
        this.strProject = this.$t('option.PROJECT')
      }
      if(this.$route.query.namespace){
        this.strNamespace = this.$route.query.namespace
        backendapi.get(`/project/${this.strProject}/namespace/${this.$route.query.namespace}/service`)
          .then(response => {
            this.tableData2 = response.data.map((item, index) => {
              item["serviceId"] = item.id;
              item["id"] = index;
              item['createdTS'] = moment(item.createdTS);
              return item;
            });
            this.disabled = false;
          })
          .catch(error => {
            console.log(error);
            this.isLoading = false;
          });
      }else{
        this.strNamespace = this.$t('option.NAMESPACE')
        this.disabled = true;
      }
    }
  },

  methods: {

    // 選取專案 //
    selectedproject() {
      var projectlist = document.getElementById("projectlist");
      this.strProject = projectlist.options[projectlist.selectedIndex].value;
      clearInterval(this.timer1);

      this.$router.push({
        path:`/admin/${this.url}/namespace`,
        query:{
          project: this.strProject
        }
      })

      this.isLoading = true;
      backendapi.get(`/project/${this.strProject}/namespace`)
        .then(response => {
          this.namespacelist = response.data;
          this.isLoading = false
          if (this.namespacelist.length >= 1) {
            this.disabled = false;
          } else {
            this.disabled = true;
          }
        })
        .catch(error => {
          console.log(error);
        });

    },

    // 選取命名空間 //
    selectednamespace() {
      var namespacelist = document.getElementById("namespacelist");
      this.strNamespace = namespacelist.options[namespacelist.selectedIndex].value;
      clearInterval(this.timer2);

      this.$router.push({
        path:'namespace',
        query:{
          project:this.$route.query.project,
          namespace: this.strNamespace
        }
      })

      this.isLoading = true;
      backendapi.get(`/project/${this.strProject}/namespace/${this.strNamespace}/service`)
        .then(response => {
          this.tableData2 = response.data.map((item, index) => {
            item["serviceId"] = item.id;
            item["id"] = index;
            item['createdTS'] = moment(item.createdTS);
            return item;
          });
          this.isLoading = false;
        })
        .catch(error => {
          alert(error.message)
          this.isLoading = false;
        });
    },

    // 接收專案 //
    getPjname(PJname){

      this.strProject = PJname
      this.isLoading = true;

      backendapi.get(`/project/${this.strProject}/namespace`)
        .then(response => {
          this.namespacelist = response.data;
          this.isLoading = false

          if (this.namespacelist.length >= 1) {
            this.disabled = false;
          } else {
            this.disabled = true;
          }
        })
        .catch(error => {
          alert(error.message)
        });
    },

    // 接收命名空間//
    getNamespace(namespace){
      this.strNamespace = namespace
      clearInterval(this.timer2);
      backendapi.get(`/project/${this.strProject}/namespace/${this.strNamespace}/configmap`).then((response) => {
        this.tableData2 = response.data.map((item, index) => {
          item["serviceId"] = item.id;
          item["id"] = index;
          item['createdTS'] = moment(item.createdTS);
          return item;
        });
      })
      .catch(error => {
        alert(error.message);
        this.isLoading = false;
      });
    },

    // 讀取表單 //
    reloadd2(){
      backendapi.get(`/project/${this.strProject}/namespace/${this.strNamespace}/service`)
        .then(response => {
          this.tableData2 = response.data.map((item, index) => {
            item["serviceId"] = item.id;
            item["id"] = index;
            item['createdTS'] = moment(item.createdTS);
            return item;
        });
      });
    },

    // 元件呼叫讀取表單 //
    loadTable() {
      this.$nextTick(() => {
        this.$refs.modal.hide();
      });
      this.timer2 = setInterval(()=> {this.reloadd2()}, 5000);
    },

    parentMethod() {
      this.$nextTick(() => {
        this.$refs.modal.hide();
      });
      this.timer2 = setInterval(()=> {this.reloadd2()}, 5000);
    },

    // 新建服務表單刪除 //
    remove2(row) {
      this.isLoading = true;
      this.tableData2.filter(item => row.id !== item.id);
      this.strservicename = this.tableData2[row.id].name;
      backendapi
        .delete(
          `/project/${this.strProject}/namespace/${this.strNamespace}/service/${this.strservicename}`
        )
        .then(response => {
          this.selectednamespace();
          this.isLoading = false;
        })
        .catch(error => {
          alert(error.message);
          this.isLoading = false;
        });
    },

    rollback(row) {
      this.tableData2.filter(item => row.id !== item.id);
      this.strservicename = this.tableData2[row.id].name;
    },
    authorize(row) {
      this.tableData2.filter(item => row.id !== item.id);
      this.strservicename = this.tableData2[row.id].name;
    },
    upgrade(row) {
      this.tableData2.filter(item => row.id !== item.id);
      this.strservicename = this.tableData2[row.id].name;
    },
    Info(row) {
      this.tableData2.filter(item => row.id !== item.id);
      this.strservicename = this.tableData2[row.id].name;
    },
    Clone(row) {
      this.tableData2.filter(item => row.id !== item.id);
      this.strservicename = this.tableData2[row.id].name;
    },
    checkCancel2(row) {
      swal({
        title: this.$t('service.SVCM_DIAL_DEL_SVC_CHECKING_MSG'),
        text: this.$t('common.MSG_IRREVERSIBLE_ACTION'),
        type: "warning",
        showCancelButton: true,
        confirmButtonColor: "#DD6B55",
        confirmButtonText: this.$t('common.BTN_CONFIRM'),
        cancelButtonText: this.$t('common.BTN_CANCEL'),
        heightAuto: false
      }).then(result => {
        if (result.value) {
          this.remove2(row);
          swal({
            title: this.$t('common.MSG_DEL_SUCCESS'),
            confirmButtonText: this.$t('common.BTN_OK'),
            type: "success",
            heightAuto: false
          });
        }
      });
    },
    viewlog(row) {
      this.logLineNumber = 50;
      this.selectedReplica = null;
      this.selectedContainer = null;
      this.logContent = "";
      this.viewAllLog = true;
      backendapi
        .get(
          `/project/${this.strProject}/namespace/${this.strNamespace}/workload/${row.serviceId}/info`
        )
        .then(res => {
          this.replicas = res.data.map((item, index) => {
           item.created = moment(item.created).format('YYYY-MM-DD HH:mm:ss')
            return item
          });;
          console.log("replicas", this.replicas);
          this.containers = res.data[0].containers.filter((item, index) => {
            if(!item["name"].includes("istio")){
              return item;
            }
          });
          console.log("containers", this.containers);
        });
    },
    query() {
      if (this.selectedReplica == null) {
        alert(this.$t('service.SVCM_ALERT_CONTAINER_LOG_NO_REPLICA_BE_SELECTED'));
      } else if (this.selectedContainer == null) {
        alert(this.$t('service.SVCM_ALERT_CONTAINER_LOG_NO_CONTAINER_BE_SELECTED'));
      } else {
        //
        var url = `/project/${this.strNamespace}/${this.selectedReplica}/${this.selectedContainer}/logs`;
        if (!this.viewAllLog) {
          url = url + `?tailLines=${this.logLineNumber}`;
        }

        backendapi
          .get(url)
          .then(res => {
            console.log(res.data);
            this.logContent = res.data.log_info.replace(/\n/g, "<br>");
          })
          .catch(err => {
            console.error(err);
          });
      }
    },

  },
  destroyed: function() {
    clearInterval(this.timer1);
    clearInterval(this.timer2);
  }
};
</script>

<style>
.VueTables tbody tr td {
  font-size: 14px;
}

#logContent{
  background: #000;
  color: #fff;
  padding-left: 10px;
  font-size: 13px;
  min-height:460px!important;
  max-height:460px!important;
  overflow: auto;
  overflow-y: scroll;
  overflow-x: hidden;
}

/* .slimScrollDiv{
  width: 100%!important;
}
.slimScrollDiv #logContent{
  width: 100%!important;
} */
table .link{
  display: none!important;
}
.name-table{
  width: 15%;
}
.state-table{
  width: 10%;
}
.date-table{
  width: 17%;
}
.action-table{
  width: 15%;
}

</style>



