<template>
<div>

    <form ref="form" @submit.stop.prevent="handleSubmit">
        <fieldset>
          <dl class="row">
            <dt class="col-sm-4">{{$t("service.REGISTRY_SELLBL_PRJ_NAME")}}</dt>
            <dd class="col-sm-8">{{strProject}}</dd>
          </dl>
        </fieldset>
        <fieldset>
            <div class="form-group row">
                <label class="col-4 col-form-label">{{$t("service.REGISTRY_DIAL_CRTECERT_IPTLBL_CERT_NAME")}}</label>
                <div class="col-8">
                    <b-form-input v-model="newRegistry.name" :class="{'form-control':true}" type="text" required/>
                </div>
            </div>
        </fieldset>
        <fieldset>
          <dl class="row">
            <dt class="col-sm-4">{{$t("service.REGISTRY_DIAL_CRTECERT_LBL_REG_ADDR")}}</dt>
            <dd class="col-sm-8">{{harbor_address}}</dd>
          </dl>
        </fieldset>
        <fieldset>
            <div class="form-group row">
                <label class="col-4 col-form-label">{{$t("service.REGISTRY_DIAL_CRTECERT_IPTLBL_ACNT")}}</label>
                <div class="col-8">
                    <b-form-input v-model="newRegistry.registries.username" :class="{'form-control':true}" type="text" required/>
                </div>
            </div>
        </fieldset>
        <fieldset>
            <div class="form-group row">
                <label class="col-4 col-form-label">{{$t("service.REGISTRY_DIAL_CRTECERT_IPTLBL_PWD")}}</label>
                <div class="col-8">
                    <b-form-input v-model="newRegistry.registries.passward" :class="{'form-control':true}" type="password" required/>
                </div>
            </div>
        </fieldset> 
        <fieldset>
            <div class="form-group row">
                <label class="col-4 col-form-label">{{$t("service.REGISTRY_DIAL_CRTECERT_IPTLBL_CONFIRM_PWD")}}</label>
                <div class="col-8">
                    <b-form-input v-model="newRegistry.registries.repassward" :class="{'form-control':true}" type="password" required/>
                </div>
            </div>
        </fieldset>                  
    </form>
    <div class="modal-footer mt-4">
      <b-button size="sm" variant="success" @click="handleSubmit()">{{$t("service.REGISTRY_DIAL_CRTECERT_BTN_SUBMIT")}}</b-button>
      <b-button size="sm" variant="danger" @click="cancel()">{{$t("service.REGISTRY_DIAL_CRTECERT_BTN_CANCEL")}}</b-button>
    </div>
  <!-- 新建鏡像庫結束 -->
  
</div>
</template>  

<script>
import Vue from 'vue';

export default {
  props:["strProject","harbor_address"],
  components: {
  },
  data(){
    return{
      newRegistry:{
        name:'',
        nameState: null,
        registries: {
            username:'',
            usernameState: null,
            passward:'',
            passwardState: null,
            repassward:'',
            repasswardState: null
        },
      },
      isLoading: false,
    };
  },
  mounted(){
    this.$emit('stopLoadTable')
    this.newRegistry.name = ''
    this.newRegistry.registries.username = ''
    this.newRegistry.registries.passward = ''
    this.newRegistry.registries.repassward = ''
    this.newRegistry.nameState = null
    this.newRegistry.registries.usernameState = null
    this.newRegistry.registries.passwardState = null
    this.newRegistry.registries.repasswardState = null
  },
  methods:{
    // 驗證 //
    checkFormValidity() {
      const valid = this.$refs.form.checkValidity();

      this.newRegistry.nameState = valid ? 'valid' : 'invalid'
      this.newRegistry.registries.usernameState = valid ? 'valid' : 'invalid'
      this.newRegistry.registries.passwardState = valid ? 'valid' : 'invalid'
      this.newRegistry.registries.repasswardState = valid ? 'valid' : 'invalid'

      if(this.newRegistry.name ==''){
        alert(this.$t('service.REGISTRY_DIAL_CRTECERT_ALERT_NO_ACCESS_CLAIM_NAME'));
      }else if (this.newRegistry.registries.username ==''){
        alert(this.$t('service.REGISTRY_DIAL_CRTECERT_ALERT_NO_USER_NAME'));
      }else if (this.newRegistry.registries.passward!==this.newRegistry.registries.repassward){
        alert(this.$t('service.REGISTRY_DIAL_CRTECERT_ALERT_INCONSISTENT_PWD'));
        this.newRegistry.registries.passward  = ""
        this.newRegistry.registries.repassward = ""
      }else{
        return valid;
      }

    },
    // 新建鏡像倉庫送出 //
    handleSubmit() {
      if (!this.checkFormValidity()) {
        return 
      }else{
        this.isLoading = true;
        backendapi.post(`/project/${this.strProject}/registry`,{
          name: this.newRegistry.name,
          registries: 
            {
              address:  this.harbor_address,
              username: this.newRegistry.registries.username,
              password: this.newRegistry.registries.passward
            }
        }).then((response) => {
          this.$emit('loadTable')
        })
        .catch(error => {
          alert(error.message);
          this.$emit('loadTable')
        });
      }
    },
    cancel(){
      this.$emit('loadTable')
    },
  },
}
</script>

<style>
#createImages .row{
  margin:0 15px;
}
fieldset:last-child{
  margin-bottom: 0;
}
</style>


