<template>
<div>

  <!-- 讀取畫面 -->
  <loading :active.sync="isLoading"></loading>

  <div class="content-heading">{{$t("sidebar.VOLUME_SET")}}</div>
 
  <!-- Select選項開始 -->
  <b-form class="was-validated">
    <div class="form-group row justify-content-end">
      <label class="col-md-2 col-form-label text-md-right">{{$t("service.SVCM_LBL_PRJ")}}:</label>
      <div class="col-md-4">
        <select id="projectlist" class="custom-select custom-select-md mb-3" @change="selectedproject()" v-model="strProject">
          <option :value="$t('option.PROJECT')" disabled selected>{{$t("option.PROJECT")}}</option>
          <option v-for="(item) in projectlist" :value="item.name" :key="item.name">{{item.name}}</option>
        </select>
      </div>
      <label class="col-md-2 col-form-label text-md-right">{{$t("service.SVCM_LBL_NAMESPACE")}}:</label>
      <div class="col-md-4">
        <select id="namespacelist" class="custom-select custom-select-md mb-3" @change="selectednamespace()" :disabled="disabled" v-model="strNamespace">
          <option id="myselected" :value="$t('option.NAMESPACE')" disabled selected>{{$t("option.NAMESPACE")}}</option>
          <option v-for="item in namespacelist" :value="item.name" :key="item.name">{{item.name}}</option>
        </select>
      </div>
    </div>
  </b-form>
  <!-- Select選項結束 -->

  <router-view v-show="!this.$route.query.namespace" @getPjname="getPjname" @getNamespace="getNamespace"></router-view>

  <!-- 新建雲硬碟開始 -->
  <b-btn type="button" class="btn btn-primary mr-1 mb-1" variant="primary" v-b-modal.createVolumes v-show="this.$route.query.namespace">{{$t("service.VOLUME_SELLBL_BTN_CRTE_VOLUME")}}</b-btn>
  <br>
  <br>
  <b-modal class="modal fade" id="createVolumes" :title="$t('service.VOLUME_SELLBL_BTN_CRTE_VOLUME')" ref="modal" :hide-footer="true">
    <NewVolume :strProject="strProject" :strNamespace="strNamespace" @loadTable="loadTable()" @stopLoadTable="stopLoadTable()"/>
  </b-modal>
  <!-- 新建雲硬碟結束 -->
  
  <!-- 分類表單開始 -->
  <v-client-table :data="tableData" :columns="columns" :options="options" v-show="this.$route.query.namespace">
    <template slot="state" slot-scope="props">
      <div>
        <div class="p-2" :class="[unactive, props.row.state=='bound'? active : '']">
          {{props.row.state}}
        </div>  
      </div>
    </template>
    <template slot="action" slot-scope="props">
      <div>
        <b-btn variant="outline-danger" class="btn-xs" @click.prevent="checkCancel(props.row)" v-b-tooltip.hover title="刪除"><i class="fas fa-trash-alt"></i></b-btn>
      </div>
    </template>
  </v-client-table>
  <!-- 分類表單結束 -->

</div>
</template>  

<script>
import Vue from 'vue';
import { ClientTable,  Event } from 'vue-tables-2';
import swal from 'sweetalert2'
import NewVolume from "./NewVolume";

Vue.use(ClientTable);

export default {
  components: {
    ClientTable,
    NewVolume
  },
  data(){
    return{
      url:'volume',
      unactive:'unactive',
      active:'active',
      timer1:'',
      selectedIndex: '',
      projectlist:[],
      strProject:'',
      namespacelist:[],
      strNamespace:'',
      strvolumesname:'',
      tableData: [],
      disabled: true,
      // showProject: true,
      // showNamespace: false,
      // showVolume: false,

      columns: ['name', 'state', 'size', 'action'],
      options: {
        sortable: ['name', 'state', 'size'],
        filterable: true,
        texts: {
          filter: this.$t('common.LBL_SEARCH'),
          filterPlaceholder:this.$t('common.IPT_HINT_ENTER_KEY_WORD'),
          count:this.$t('common.LBL_TOTAL') + "Showing {from} to {to} of {count} records|{count}" + this.$t('common.LBL_RECORDS'),
          noResults:this.$t('common.LBL_NO_RECORD'),
        },
        headings: {
            name: () => this.$t("service.VOLUME_SELLBL_LST_VOLUME_NAME"),
            state: () => this.$t("service.VOLUME_SELLBL_LST_STATUS"),
            size: () => this.$t("service.VOLUME_SELLBL_LST_QUOTA"),
            action: () => this.$t("service.VOLUME_SELLBL_LST_OPT"),
        },
        columnsClasses:{
          state: "state",
        },
        pagination: { chunk: 5 },
        sortIcon: {
            is: 'fa-sort',
            base: 'fas',
            up: 'fa-sort-up',
            down: 'fa-sort-down'
        },
      },
      isLoading: false,
    };
  },
  mounted(){
    this.strProject = this.$t('option.PROJECT')
    this.strNamespace = this.$t('option.NAMESPACE')
    this.$router.push({
      path:'/admin/volume/project',
      
    })
    
    this.isLoading = true;
    backendapi.get("/project").then((response) => {
      this.isLoading = false;
      this.projectlist = response.data;
    })
    .catch(error => {
      alert(error.message);
      this.isLoading = false;
    });

  },

  watch:{
    $route (to, from){
      if(location.pathname=="/admin/volume"){
        this.$router.push({
          path:'/admin/volume/project',
          params:{
            project:"project"
          }
        })
      }
      if(this.$route.query.project){
        this.strProject = this.$route.query.project
      }else{
        this.strProject = this.$t('option.PROJECT')
      }
      if(this.$route.query.namespace){
        this.strNamespace = this.$route.query.namespace
        backendapi.get(`/project/${this.strProject}/namespace/${this.strNamespace}/persistentvolumeclaim`)
          .then(response => {
            this.tableData = response.data.map((item, index) => {
                item['id'] = index
                return item
            });
            this.disabled = false;
          })
          .catch(error => {
            console.log(error);
            this.isLoading = false;
          });
      }else{
        this.strNamespace = this.$t('option.NAMESPACE')
        this.disabled = true;
      }
    }
  },

  methods:{

    // 選取專案 //
    selectedproject() {
      var projectlist = document.getElementById("projectlist");
      this.strProject = projectlist.options[projectlist.selectedIndex].value;
      clearInterval(this.timer1);

      this.$router.push({
        path:`/admin/${this.url}/namespace`,
        query:{
          project: this.strProject
        }
      })

      this.isLoading = true;
      backendapi.get(`/project/${this.strProject}/namespace`).then(response => {
        this.namespacelist = response.data;
        this.isLoading = false
        if (this.namespacelist.length >= 1) {
          this.disabled = false;
        } else {
          this.disabled = true;
        }
      })
      .catch(error => {
        alert(error.message);
        this.isLoading = false
      });
      
    },

    // 選取命名空間 //
    selectednamespace() {
      var namespacelist = document.getElementById("namespacelist");
      this.strNamespace = namespacelist.options[namespacelist.selectedIndex].value;
      clearInterval(this.timer1);

      this.$router.push({
        path:'namespace',
        query:{
          project:this.$route.query.project,
          namespace: this.strNamespace
        }
      })

      backendapi.get(`/project/${this.strProject}/namespace/${this.strNamespace}/persistentvolumeclaim`).then((response) => {
        this.tableData = response.data.map((item, index) => {
            item['id'] = index
            return item
        });
      })
      .catch(error => {
        alert(error.message)
        this.isLoading = false;
      });

    },    

    // 接收專案 //
    getPjname(PJname){
      this.strProject = PJname
      this.isLoading = true;
      clearInterval(this.timer1);
      backendapi.get(`/project/${this.strProject}/namespace`).then(response => {
        this.namespacelist = response.data;
        this.isLoading = false
        if (this.namespacelist.length >= 1) {
          this.disabled = false;
        } else {
          this.disabled = true;
        }
      })
      .catch(error => {
        console.log(error);
      });
    },

    // 接收命名空間//
    getNamespace(namespace){
      this.isLoading = true;
      this.strNamespace = namespace
      clearInterval(this.timer1);
      backendapi.get(`/project/${this.strProject}/namespace/${this.strNamespace}/persistentvolumeclaim`).then((response) => {
        this.tableData = response.data.map((item, index) => {
            item['id'] = index
            return item
        });
        this.isLoading = false;
      })
      .catch(error => {
        alert(error.message)
        this.isLoading = false;
      });
    },

    // 元件呼叫讀取表單 //
    loadTable() {
      this.$nextTick(() => {
        this.$refs.modal.hide();
      });
      this.timer1 = setInterval(()=> {this.reloadd()}, 5000);
    },

    // 元件呼叫停止讀取表單 //
    stopLoadTable(){
      clearInterval(this.timer1);
    },    

    // 讀取表單 //
    reloadd(){
      backendapi.get(`/project/${this.strProject}/namespace/${this.strNamespace}/persistentvolumeclaim`).then((response) => {
        this.tableData = response.data.map((item, index) => {
            item['id'] = index
            return item
        });
      })
    },

    // 分類表單刪除 //
    remove (row) {
      this.tableData.filter(item => row.id !== item.id);
      this.strvolumesname = this.tableData[row.id].name;
      backendapi.delete(`/project/${this.strProject}/namespace/${this.strNamespace}/persistentvolumeclaim/${this.strvolumesname}`).then(response => {
        this.selectednamespace();
        this.isLoading = false;      
      })
      .catch(error => {
        alert(error.message);
        this.isLoading = false;
      });
    },
    // 刪除步驟 //
    checkCancel(row) { 
      swal({
          title: this.$t('service.VOLUME_DIAL_DEL_VOLUME_MSG'),
          text: this.$t('common.MSG_IRREVERSIBLE_ACTION'),
          type: "warning",
          showCancelButton: true,
          confirmButtonColor: "#DD6B55",
          confirmButtonText: this.$t('common.BTN_CONFIRM'),
          cancelButtonText:this.$t('common.BTN_CANCEL'),
          heightAuto: false
      }).then((result) => {
          if(result.value){
          this.remove(row);
            swal({
                title: this.$t('common.MSG_DEL_SUCCESS'),
                confirmButtonText: this.$t('common.BTN_OK'),
                type: "success",
                heightAuto: false
            });
          } 
      });
    },
  },
 destroyed: function() {
    console.log('volume timer destroyed');
    clearInterval(this.timer1);
  }
}
</script>

<style>
#createVolumes .row{
  margin:0 15px;
}
fieldset:last-child{
  margin-bottom: 0;
}
</style>


