<template>
<div>

    <form ref="form" @submit.stop.prevent="handleSubmit">
      <fieldset>
          <div class="form-group row align-items-end">
              <label class="col-5 col-form-label">{{$t("service.VOLUME_DIAL_CRTEVOL_IPTLBL_VOLUME_NAME")}}</label>
              <div class="col-6">
                  <b-form-input v-model="newVolumes.name" :class="{'form-control':true}" type="text" required />
              </div>
          </div>
      </fieldset>
      <fieldset>
          <div class="form-group row align-items-end">
              <label class="col-5 col-form-label">{{$t("service.VOLUME_DIAL_CRTEVOL_SELLBL_VOLUME_QUOTA")}}</label>
              <div class="col-5">
                  <b-form-input v-model="newVolumes.size" :class="{'form-control':true}" type="number" min="1" />
              </div>
              <div class="col-1">
                <span>Gi</span>
              </div>
          </div>
      </fieldset>
      <fieldset>
          <div class="form-group row align-items-end">
              <label class="col-5 col-form-label">{{$t("service.VOLUME_DIAL_CRTEVOL_SELLBL_VOLUME_TYPE")}}</label>
              <div class="col-6">
                  <select id="storageClasslist" class="custom-select custom-select-sm" @change="selectedstorage()">
                    <option value="" selected disabled>{{$t('service.VOLUME_HINT_SEL_VOLUME_TYPE')}}</option>
                    <option v-for="(item) in newVolumes.storageClassId" :value="item.name" >{{item.name}}</option>
                  </select>
              </div>
          </div>
      </fieldset>             
    </form>
    <div class="modal-footer mt-4">
      <b-button size="sm" variant="success" @click="handleSubmit()">{{$t("service.VOLUME_DIAL_CRTEVOL_BTN_SUBMIT")}}</b-button>
      <b-button size="sm" variant="danger" @click="cancel()">{{$t("service.REGISTRY_DIAL_CRTECERT_BTN_CANCEL")}}</b-button>
    </div>

</div>
</template>  

<script>
import Vue from 'vue';


export default {
  props:["strProject" , "strNamespace"],
  components: {
  },
  data(){
    return{

      newVolumes:{
        name:'',
        nameState:null,
        size:'1',
        storageClassId:[
          {
            name:'sata'
          },
          {
            name:'ssd-200'
          },
          {
            name:'ssd-400'
          },
          {
            name:'ssd-600'
          }
        ],
        selectedstorage:''
      },

      isLoading: false,
    };
  },
  mounted(){
    this.$emit('stopLoadTable')
    this.newVolumes.name = ''
    this.newVolumes.nameState = null
  },
  methods:{

    // 選取雲硬碟類型 //
    selectedstorage(){
      var storageClasslist = document.getElementById("storageClasslist");
      this.newVolumes.selectedstorage = storageClasslist.options[storageClasslist.selectedIndex].value;
    },

    // 驗證 //
    checkFormValidity() {
      const vm = this;
      const valid = vm.$refs.form.checkValidity();
      this.newVolumes.nameState = valid ? 'valid' : 'invalid'
      return valid
    },

    // 新建雲硬碟送出 //
    handleSubmit() {
      if (!this.checkFormValidity()) {
        return
      }
      backendapi.post(`/project/${this.strProject}/namespace/${this.strNamespace}/persistentvolumeclaim`,{
        name: this.newVolumes.name,
        size: this.newVolumes.size + 'Gi',
        storageClassId: this.newVolumes.selectedstorage
      }).then((response) => {
        this.$emit('loadTable')
      })
      .catch(error => {
        this.$emit('loadTable')
      });

    },
    cancel(){
      this.$emit('loadTable')
    },
  },
}
</script>

<style>
#createVolumes .row{
  margin:0 15px;
}
fieldset:last-child{
  margin-bottom: 0;
}
</style>


