<template>
  <div>

    <b-btn type="button" class="btn btn-primary mr-1 mb-1" variant="primary"  v-b-modal.createNamespace>{{$t("service.SVCM_BTN_NEW_NAMESPACE")}}</b-btn>
    <br />
    <br />

    <!-- Namespace表單開始 -->
    <v-client-table :data="tableData1" :columns="columns1" :options="options1">
      <template slot="name" slot-scope="props">
        <div>
          <div class="text-left">
            <a href="javascript:void(0)" @click.prevent="goNamespace(props.row.name)">{{props.row.name}}</a>
          </div>  
        </div>
      </template>
      <template slot="state" slot-scope="props">
        <div>
          <div class="p-2" :class="[unactive, props.row.state=='active'? active : '']">
            {{props.row.state}}
          </div>  
        </div>
      </template>
      <template slot="action" slot-scope="props">
        <div>
          <!-- <b-btn variant="outline-info" class="btn-xs" v-b-tooltip.hover :title="$t('common.ICON_TIP_TRACE')" @click.prevent="goTrace(props.row.name)">
            <i class="fas fa-link"></i>
          </b-btn> -->
          <b-btn variant="outline-info" class="btn-xs" v-b-modal.linklink v-b-tooltip.hover :title="$t('common.ICON_TIP_TRACE')" @click="goTrace(props.row)">
            <i class="fas fa-link"></i>
          </b-btn>
          <!-- <b-btn variant="outline-info" class="btn-xs" v-if="props.row.state=='active'" >
            <a :href="props.row.traceroute_link" target="blank">
              <i class="fas fa-link"></i>
            </a>
          </b-btn> -->
          <b-btn variant="outline-info" class="btn-xs" v-if="props.row.state=='active'" @click.prevent="editpop(props.row)" v-b-modal.editNamespace v-b-tooltip.hover :title="$t('common.ICON_TIP_EDIT')">
              <i class="fas fa-edit"></i>
          </b-btn>
          <b-btn variant="outline-danger" class="btn-xs" v-if="props.row.state!=='removing'" @click.prevent="checkCancel(props.row)" v-b-tooltip.hover :title="$t('common.ICON_TIP_DEL')">
            <i class="fas fa-trash-alt"></i>
          </b-btn>
        </div>
      </template>
    </v-client-table>
    <!-- Namespace表單結束 -->

    <!-- 新建Namespace開始 -->
    <b-modal class="modal fade" id="createNamespace" size="xl" :title="$t('service.SVCM_DIAL_NEWNAMESPACE_TITLE')" ref="modal" :hide-footer="true">
      <NewNamespace :strProject="strProject" :strNamespace="strNamespace" v-on:childMethod="loadProject()"/>
    </b-modal>
    <!-- 新建Namespace結束 -->

    <!-- 編輯Namespace開始 -->
    <b-modal class="modal fade" id="editNamespace" size="xl" :title="$t('service.SVCM_DIAL_MODIFY_NEWNAMESPACE_TITLE')" ref="modal" :hide-footer="true">
      <EditNamespace :strProject="strProject" :strNamespace="strNamespace" v-on:childMethod="loadProject()"/>
    </b-modal>
    <!-- 編輯Namespace結束 -->

    
    <!-- 操作項的調用練追蹤popup開始 -->
    <b-modal class="modal fade" id="linklink" size="xl" :title="$t('service.SVCM_DIAL_TRACEING_TITLE')" ref="modal" :hide-footer="true">
      <embed width="100%" height="650" :src="traceUrl" frameborder="0" allowfullscreen></embed> 
    </b-modal>
    <!-- 操作項的調用練追蹤popup結束 -->


  </div>
</template>

<script>
import Vue from "vue";
import { ClientTable, Event } from "vue-tables-2";
import swal from "sweetalert2";
import NewNamespace from "./NewNamespace";
import EditNamespace from "./EditNamespace";

Vue.use(ClientTable);

export default {
  // props: ['strProject','strNamespace'],
  // props:["namespace"],
  props: ['strProject'],
  components: {
    ClientTable,
    NewNamespace,
    EditNamespace,
  },
  data() {
    return {
      reversProxyUrl:'',
      clusterid:'',
      timer1:'',
      strNamespace :'',
      strTrace:'',
      traceUrl:'',
      unactive:'unactive',
      active:'active',
      tableData1: [],
      columns1: ["name", "state", "description", "createdTS", "action", "traceroute_link"],
      options1: {
        sortable: ["name", "state", "description", "createdTS"],
        filterable: true,
        texts: {
          filter: this.$t('common.LBL_SEARCH'),
          filterPlaceholder: this.$t('common.IPT_HINT_ENTER_KEY_WORD'),
          count: this.$t('common.LBL_TOTAL')  + "Showing {from} to {to} of {count} records|{count}" + this.$t('common.LBL_RECORDS'),
          noResults:this.$t('common.LBL_NO_RECORD')
        },
        headings: {
          name: () => this.$t('service.SVCM_LST_NAMESPACE_NAME'),
          state: () => this.$t('service.SVCM_LST_NAMESPACE_STATUS'),
          description: () => this.$t('service.SVCM_LST_NAMESPACE_DESC'),
          createdTS: () => this.$t('service.SVCM_LST_NAMESPACE_CREATE_TIME'),
          action: () => this.$t('service.SVCM_LST_NAMESPACE_OPT')
        },
        columnsClasses:{
          name:"name-table",
          state: "state-table",
          description:"description-table",
          action:"action-table",
          createdTS: "date-table",
          traceroute_link: "link"
        },
        pagination: { chunk: 5 },
        sortIcon: {
          is: "fa-sort",
          base: "fas",
          up: "fa-sort-up",
          down: "fa-sort-down"
        },
        dateColumns:['createdTS'],
        dateFormat: 'YYYY-MM-DD HH:mm:ss',
        orderBy:{
          column:"createdTS",
          ascending:false
        }, 
      },
    };
  },
  mounted(){
    this.reversProxyUrl = reversProxyUrl
    this.clusterid = localStorage.getItem('clusterid')
    clearInterval(this.timer1);
    backendapi.get(`/project/${this.$route.query.project}/namespace`).then(response => {
      this.tableData1 = response.data.map((item, index) => {
        item["id"] = index;
        item['createdTS'] = moment(item.createdTS);
        return item;
      });
    })
    .catch(error => {
      console.log(error);
    });
  },
  watch:{
    $route (to, from){
      backendapi.get(`/project/${this.$route.query.project}/namespace`).then(response => {
        this.tableData1 = response.data.map((item, index) => {
          item["id"] = index;
          item['createdTS'] = moment(item.createdTS);
          return item;
        });
      })
      .catch(error => {
        console.log(error);
      });
    }
  },
  methods: {
    
    goNamespace(name){
      this.$emit('getNamespace', name)
      this.$router.push({
        name:'',
        query:{
          project:this.$route.query.project,
          namespace: name
        }
      })
    },

    goTrace(row){
      this.strTrace = row.name
      this.traceUrl = this.reversProxyUrl + this.clusterid + '/kiali/console/graph/namespaces/?edges=requestsPerSecond&graphType=versionedApp&namespaces=' + this.strTrace + '&unusedNodes=true&injectServiceNodes=false&pi=15000&duration=60&layout=dagre&kiosk=true'
    },

    loadProject() {
      this.$nextTick(() => {
        this.$refs.modal.hide();
      });
      clearInterval(this.timer1);
      this.timer1 = setInterval(()=> {this.reloadd()}, 5000);
    },

    reloadd(){
      backendapi.get(`/project/${this.strProject}/namespace`)
        .then(response => {
          this.tableData1 = response.data.map((item, index) => {
            item["id"] = index;
            item['createdTS'] = moment(item.createdTS);
            return item;
          });
        })
    },

    editpop(row) {
      this.tableData1.filter(item => row.id !== item.id);
      this.strNamespace = this.tableData1[row.id].name;
    },

    checkCancel(row) {
      swal({
        title: this.$t('service.SVCM_DIAL_DEL_NAMESPACE_MSG'),
        text: this.$t('common.MSG_IRREVERSIBLE_ACTION'),
        type: "warning",
        showCancelButton: true,
        confirmButtonColor: "#DD6B55",
        confirmButtonText: this.$t('common.BTN_CONFIRM'),
        cancelButtonText:  this.$t('common.BTN_CANCEL'),
        heightAuto: false
      }).then(result => {
        if (result.value) {
          this.remove(row);
          swal({
            title: this.$t('service.SVCM_DIAL_DEL_NAMESPACE_SUCCESS'),
            confirmButtonText: this.$t('common.BTN_OK'),
            type: "success",
            heightAuto: false
          });
        }
      });
    },

    remove(row) {
      this.isLoading = true;
      this.tableData1.filter(item => row.id !== item.id);
      this.strspacename = this.tableData1[row.id].name;
      backendapi
        .delete(`/project/${this.strProject}/namespace/${this.strspacename}`)
        .then(response => {
          this.loadTable();
          this.isLoading = false;
        })
        .catch(error => {
          console.log(error);
        });
    },

  }

};
</script>


<style>
#tableResource.table input {
  max-width: 85%;
  display: inline;
}
#tableResource.table .thead-dark th {
  background-color: #3f51b5;
}

#projectTable.vuetable th#__state {
    width: 300px;
}

.description-table{
  width: 40%;
}
</style>



