import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/login/Login'
import Center from '@/components/common/Center'
import Admin from '@/components/common/Admin'
import Dashboard from '@/components/dashboard/Dashboard'
import User from '@/components/user/User'
import Project from '@/components/project/Project'
import Resource from '@/components/resource/Resource'
import Volume from '@/components/volume/Volume'
import Service from '@/components/service/Service'
import ServiceProjectTable from '@/components/service/ProjectTable'
import ServiceNamespaceTable from '@/components/service/NamespaceTable'
import ConfigMap from '@/components/configmap/ConfigMap'
import Ingress from '@/components/ingress/Ingress'
import Pipeline from '@/components/pipeline/Pipeline'
import Registry from '@/components/registry/Registry'
import ProjectTable from '@/components/table/ProjectTable'
import NamespaceTable from '@/components/table/NamespaceTable'
Vue.use(Router)
export default new Router({
  routes: [{
      path: '/center',
      name: 'Center',
      component: Center,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/admin',
      name: 'Admin',
      component: Admin,
      meta: {
        requiresAuth: true
      },
      children: [
        {
          path: 'dashboard',
          name: 'dashboard',
          component: Dashboard,
          meta: {
            requiresAuth: true,
          },
        },
        {
          path: 'user',
          name: 'user',
          component: User,
          meta: {
            requiresAuth: true,
          },
        },
        {
          path: 'project',
          name: 'project',
          component: Project,
          meta: {
            requiresAuth: true
          },
        },
        {
          path: 'resource',
          name: 'resource',
          component: Resource,
          meta: {
            requiresAuth: true
          },
        },
        {
          path: 'volume',
          name: 'volume',
          component: Volume,
          meta: {
            requiresAuth: true
          },
          children:[
            {
              path: 'project',
              component: ProjectTable,
              props: true
            },
            {
              path: 'namespace',
              component: NamespaceTable,
              props: true
            }
          ],
        },        
        {
          path: 'service',
          name: 'service',
          component: Service,
          meta: {
            requiresAuth: true
          },
          children:[
            {
              path: 'project',
              component: ServiceProjectTable,
              props: true
            },
            {
              path: 'namespace',
              component: ServiceNamespaceTable,
              props: true
            }
          ]
        },
        {
          path: 'configmap',
          name: 'configmap',
          component: ConfigMap,
          meta: {
            requiresAuth: true
          },
          children:[
            {
              path: 'project',
              component: ProjectTable,
              props: true
            },
            {
              path: 'namespace',
              component: NamespaceTable,
              props: true
            }
          ],
        },
        {
          path: 'ingress',
          name: 'ingress',
          component: Ingress,
          meta: {
            requiresAuth: true
          },
          children:[
            {
              path: 'project',
              component: ProjectTable,
              props: true
            },
            {
              path: 'namespace',
              component: NamespaceTable,
              props: true
            }
          ],
        },
        {
          path: 'pipeline/:auth?/:gits?/:project?/:id?',
          name: 'pipeline',
          component: Pipeline,
          meta: {
            requiresAuth: true
          },
          children:[
            {
              path: 'project',
              component: ProjectTable,
              props: true
            }
          ],
        },
        {
          path: 'registry',
          name: 'registry',
          component: Registry,
          meta: {
            requiresAuth: true
          },
          children:[
            {
              path: 'project',
              component: ProjectTable,
              props: true
            }
          ],
        },
      ],
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '/',
      component: Login
    },
  ]
})



