<template>
<div>

  <!-- 讀取畫面 -->
  <loading :active.sync="isLoading"></loading>

  <div class="content-heading">{{$t("sidebar.REGISTRY")}}</div>

  <!-- 鏡像倉庫地址開始 -->
  <dl class="row" v-show="this.$route.query.id">
    <dt style="margin-left:15px;">{{$t("service.REGISTRY_LBL_REGISTRY_ADDR")}}:</dt>
    <dd class="col-sm-10"><a :href="'http://'+ this.harbor_address" target="_blank">{{this.harbor_address}}</a></dd>
  </dl>
  <!-- 鏡像倉庫地址結束 -->

  <!-- Select選項開始 -->
  <b-form class="was-validated">
    <div class="form-group row justify-content-end">
      <label class="col-md-2 col-form-label text-md-right">{{$t("service.SVCM_LBL_PRJ")}}:</label>
      <div class="col-md-4">
        <select id="projectlist" class="custom-select custom-select-md mb-3" @change="selectedproject()" v-model="strProject">
          <option :value="$t('option.PROJECT')" disabled selected>{{$t("option.PROJECT")}}</option>
          <option v-for="(item) in projectlist" :value="item.name" :key="item.name">{{item.name}}</option>
        </select>
      </div>
    </div>
  </b-form>
  <!-- Select選項結束 -->

  <router-view v-show="!this.$route.query.id" :tableStep="tableStep" @getPjname="getPjname"></router-view>

  <!-- 新建鏡像庫開始 -->
  <b-btn type="button" class="btn btn-primary mr-1 mb-1" variant="primary" v-b-modal.createImages v-show="this.showImage">{{$t("service.REGISTRY_BTN_CRTE_REG_CERT")}}</b-btn>
  <br>
  <br>
  <b-modal class="modal fade" id="createImages" :title="$t('service.REGISTRY_BTN_CRTE_REG_CERT')" ref="modal" :hide-footer="true">
    <NewRegistry :strProject="strProject" :harbor_address="harbor_address" @loadTable="loadTable()" @stopLoadTable="stopLoadTable()"/>
  </b-modal>
  <!-- 新建鏡像庫結束 -->
  
  <!-- 分類表單開始 -->
  <v-client-table :data="tableData" :columns="columns" :options="options" v-show="this.showImage">
    <template slot="action" slot-scope="props">
      <div>
        <b-btn variant="outline-danger" class="btn-xs" @click.prevent="checkCancel(props.row)" v-b-tooltip.hover title="刪除"><i class="fas fa-trash-alt"></i></b-btn>
      </div>
    </template>
  </v-client-table>
  <!-- 分類表單結束 -->

  <RegistryContent :strProject="strProject" :harbor_address="harbor_address" @loadTable="loadTable()" @stopLoadTable="stopLoadTable()" v-if="this.$route.query.id"/>

</div>
</template>  

<script>
import Vue from 'vue';
import { ClientTable,  Event } from 'vue-tables-2';
import swal from 'sweetalert2'
import NewRegistry from "./NewRegistry";
import RegistryContent from "./RegistryContent";

Vue.use(ClientTable);

export default {
  components: {
    ClientTable,
    RegistryContent
  },
  data(){
    return{
      timer:'',
      tableStep:'1',
      selectedIndex:'',
      projectlist:[],
      strProject:'',
      harbor_address:'',
      tableData: [],
      // showProject:true,
      showImage: false,

      columns: ['name', 'username', 'action'],
      options: {
        sortable: ['name', 'username', 'date'],
        filterable: true,
        texts: {
          filter: this.$t('common.LBL_SEARCH'),
          filterPlaceholder:this.$t('common.IPT_HINT_ENTER_KEY_WORD'),
          count: this.$t('common.LBL_TOTAL') + "Showing {from} to {to} of {count} records|{count}" + this.$t('common.LBL_RECORDS'),
          noResults:this.$t('common.LBL_NO_RECORD'),
        },
        headings: {
            name: () => this.$t('service.REGISTRY_DIAL_CRTECERT_IPTLBL_CERT_NAME'),
            username: () => this.$t('service.REGISTRY_DIAL_CRTECERT_IPTLBL_ACNT'),
            action: () => this.$t('service.REGISTRY_CERTLST_OPT'),
        },
        pagination: { chunk: 5 },
        sortIcon: {
            is: 'fa-sort',
            base: 'fas',
            up: 'fa-sort-up',
            down: 'fa-sort-down'
        },
      },
      isLoading: false,
    };
  },
  mounted(){
    this.strProject = this.$t('option.PROJECT')
    this.$router.push({
      path:'/admin/registry/project',
    })
    this.isLoading = true;
    backendapi.get("/project").then((response) => {
      this.isLoading = false;
      this.projectlist = response.data;
    })
    .catch(error => {
      alert(error.message)
    });
  },

  watch:{
    $route (to, from){
      if(location.pathname=="/admin/registry"){
        this.$router.push({
          path:'/admin/registry/project',
          params:{
            project:"project"
          }
        })
      }
      if(this.$route.query.id){
        this.strProject = this.$route.query.id
        backendapi.get(`/project/${this.strProject}/registry`)
          .then(response => {
            this.harbor_address = response.data.harbor_address
            const b = response.data.credentail_list.map(item => {
              for(let i in item["registries"]){
                return item["registries"][i]
              }
            })
            const c = response.data.credentail_list.map(item => {
              return item
            })
            let merged = [];
            for(let i=0; i<b.length; i++) {
              merged.push({...b[i],...(c[i])});
            }
            this.tableData = merged.map((item, index) => {
              item["id"] = index;
              return item;
            });
          })
          .catch(error => {
            console.log(error);
            this.isLoading = false;
          });
      }else{
        this.strProject = this.$t('option.PROJECT')
      }
    }
  },
  
  methods:{
    upconfirm(){
      console.log(this.create_project)
    },
    // 選取專案 //
    selectedproject() {
        var projectlist = document.getElementById("projectlist");
        this.strProject = projectlist.options[projectlist.selectedIndex].value;
        clearInterval(this.timer1);

        this.$router.push({
          name:'',
          query:{
            id: this.strProject
          },
        })
    },

    getPjname(PJname){
      this.strProject = PJname
      this.isLoading = true;
      clearInterval(this.timer1);
        backendapi.get(`/project/${this.strProject}/registry`).then((response) => {
          this.harbor_address = response.data.harbor_address
          // this.project_role = response.data.project_role

          // const b = response.data.credentail_list.map(item => {
          //   for(let i in item["registries"]){
          //     return item["registries"][i]
          //   }
          // })

          // const c = response.data.credentail_list.map(item => {
          //   return item
          // })
          
          // let merged = [];

          // for(let i=0; i<b.length; i++) {
          //   merged.push({...b[i],...(c[i])});
          // }

          // this.tableData = merged.map((item, index) => {
          //   item["id"] = index;
          //   return item;
          // });          

          this.isLoading = false;

        })
        .catch(error => {
          this.isLoading = false;
          alert(error.message)
        });
    },

    // 元件呼叫讀取表單 //
    loadTable() {
      this.$nextTick(() => {
        this.$refs.modal.hide();
      });
      this.timer1 = setInterval(()=> {this.reloadd()}, 5000);
    },

    // 元件呼叫停止讀取表單 //
    stopLoadTable(){
      clearInterval(this.timer1);
    },

    // 讀取表單 //
    reloadd(){
      backendapi.get(`/project/${this.strProject}/registry`).then((response) => {
        this.harbor_address = response.data.harbor_address
        const b = response.data.credentail_list.map(item => {
          for(let i in item["registries"]){
            return item["registries"][i]
          }
        })
        const c = response.data.credentail_list.map(item => {
          return item
        })
        let merged = [];
        for(let i=0; i<b.length; i++) {
          merged.push({...b[i],...(c[i])});
        }
        this.tableData = merged.map((item, index) => {
          item["id"] = index;
          return item;
        });
      })
    },
    // 分類表單刪除 //
    remove (row) {
      this.tableData.filter(item => row.id !== item.id);
      this.strimagename = this.tableData[row.id].name;
      backendapi.delete(`/project/${this.strProject}/registry/${this.strimagename}`).then(response => {
        this.timer1 = setInterval(()=> {this.reloadd()}, 5000);
        this.isLoading = false;
          
        })
        .catch(error => {
          alert(error.message);
          this.isLoading = false;
        });
    },
    // 刪除步驟 //
    checkCancel(row) { 
      swal({
          title: this.$t('service.REGISTRY_DIAL_DELCERT_MSG'),
          text: this.$t('common.MSG_IRREVERSIBLE_ACTION'),
          type: "warning",
          showCancelButton: true,
          confirmButtonColor: "#DD6B55",
          confirmButtonText: this.$t('common.BTN_CONFIRM'),
          cancelButtonText: this.$t('common.BTN_CANCEL'),
          heightAuto: false
      }).then((result) => {
          if(result.value){
          this.remove(row);
            swal({
                title: this.$t('common.MSG_DEL_SUCCESS'),
                confirmButtonText: this.$t('common.BTN_OK'),
                type: "success",
                heightAuto: false
            });
          } 
      });
    },
  },
   destroyed: function() {
    console.log('image timer destroyed');
    clearInterval(this.timer1);
  }
}
</script>

<style>
#createImages .row{
  margin:0 15px;
}
fieldset:last-child{
  margin-bottom: 0;
}
.must_write{
  color:red;
  font-size: 13px;
  margin-left: 5px;
}
</style>


