<template>
<div>

  <!-- 讀取畫面 -->
  <loading :active.sync="isLoading"></loading>

  <div class="content-heading">{{$t("sidebar.INGRESS")}}</div>
 
  <!-- select選項開始 -->
  <b-form class="was-validated">
    <div class="form-group row">
      <label class="col-md-2 col-form-label text-md-right">{{$t("service.SVCM_LBL_PRJ")}}:</label>
      <div class="col-md-4">
        <select id="projectlist" class="custom-select custom-select-md mb-3" @change="selectedproject()" v-model="strProject">
          <option :value="$t('option.PROJECT')" disabled selected>{{$t("option.PROJECT")}}</option>
          <option v-for="(item) in projectlist" :value="item.name" :key="item.name">{{item.name}}</option>
        </select>
      </div>
      <label class="col-md-2 col-form-label text-md-right">{{$t("service.SVCM_LBL_NAMESPACE")}}:</label>
      <div class="col-md-4">
        <select id="namespacelist" class="custom-select custom-select-md mb-3" @change="selectednamespace()" :disabled="disabled" v-model="strNamespace">
          <option id="myselected" :value="$t('option.NAMESPACE')" disabled selected>{{$t("option.NAMESPACE")}}</option>
          <option v-for="item in namespacelist" :value="item.name" :key="item.name">{{item.name}}</option>
        </select>
      </div>
    </div>
  </b-form>
  <!-- select選項結束 -->

  <router-view v-show="!this.$route.query.namespace" @getPjname="getPjname" @getNamespace="getNamespace"></router-view>

  <!-- 新建Ingress開始 -->
  <b-modal class="modal fade" id="createIngress" :title="$t('service.INGRS_BTN_CRTE_INGRS')" ref="modal" size="xl" :hide-footer="true">
    <NewIngress :strProject="strProject" :strNamespace="strNamespace" :ip="ip" @loadTable="loadTable()" @stopLoadTable="stopLoadTable()"/>
  </b-modal>
  <!-- 新建Ingress結束 --> 


 <!-- 分類表單開始 -->
  <b-btn type="button" class="btn btn-primary mr-1 mb-1" variant="primary"  v-b-modal.createIngress v-show="this.$route.query.namespace">{{$t("service.INGRS_BTN_CRTE_INGRS")}}</b-btn>
  <br>
  <br>
  <v-client-table :data="tableData" :columns="columns" :options="options" v-show="this.$route.query.namespace">
    <template slot="state" slot-scope="props">
      <div>
        <div class="p-2" :class="[unactive, props.row.state=='active'? active : '']">
          {{props.row.state}}
        </div>  
      </div>
    </template>
    <template slot="action" slot-scope="props">
      <div>
        <b-btn variant="outline-info" class="btn-xs" v-if="props.row.state=='active'" @click.prevent="editpop(props.row)"  v-b-modal.editIngress v-b-tooltip.hover :title="$t('common.ICON_TIP_EDIT')"><i class="fas fa-edit"></i></b-btn>
        <b-btn variant="outline-danger" class="btn-xs" @click.prevent="checkCancel(props.row)"><i class="fas fa-trash-alt" v-b-tooltip.hover :title="$t('common.ICON_TIP_DEL')"></i></b-btn>
      </div>
    </template>
  </v-client-table>
  <!-- 分類表單結束 -->


  <!-- 編輯Ingress開始 -->
  <b-modal class="modal fade" id="editIngress" :title="$t('service.INGRS_DIAL_EDITINGRES_TITLE')" ref="modal" size="xl" :hide-footer="true">
    <EditIngress :strProject="strProject" :strNamespace="strNamespace" :strIngress="strIngress" @loadTable="loadTable()" @stopLoadTable="stopLoadTable()"/>
  </b-modal>
  <!-- 編輯Ingress結束 -->
  

</div>
</template>  

<script>
import Vue from 'vue';
import { ClientTable,  Event } from 'vue-tables-2';
import swal from 'sweetalert2'
import NewIngress from "./NewIngress";
import EditIngress from "./EditIngress";

import VeeValidate from 'vee-validate';
Vue.use(VeeValidate, {
    fieldsBagName: 'formFields'  // fix issue with b-table
})

Vue.use(ClientTable);

export default {
  components: {
    ClientTable,
    NewIngress,
    EditIngress
  },
  data(){
    return{
      url:'ingress',
      unactive:'unactive',
      active:'active',
      timer1:'',
      name:'',
      selectedIndex: '',
      projectlist:[],
      strProject:'',
      namespacelist:[],
      strNamespace:'',
      strIngress:'',
      // showProject: true,
      // showNamespace: false,
      // showIngress: false,
      disabled: true,
      ip:'',
      tableData: [],
      columns: ['name','state', 'host', 'action'],
      options: {
        sortable: ['name', 'host'],
        filterable: true,
        texts: {
          filter: this.$t('common.LBL_SEARCH'),
          filterPlaceholder:this.$t('common.IPT_HINT_ENTER_KEY_WORD'),
          count:this.$t('common.LBL_TOTAL') + "Showing {from} to {to} of {count} records|{count}" + this.$t('common.LBL_RECORDS'),
          noResults:this.$t('common.LBL_NO_RECORD'),
        },
        headings: {
            name: () => this.$t('service.INGRS_LST_INGRS_NAME'),
            state: () => this.$t('service.VOLUME_SELLBL_LST_STATUS'),
            host: () => this.$t('service.INGRS_LST_ROUTE'),
            action: () => this.$t('service.INGRS_LST_OPT'),
        },
        pagination: { chunk: 5 },
        sortIcon: {
            is: 'fa-sort',
            base: 'fas',
            up: 'fa-sort-up',
            down: 'fa-sort-down'
        },
        templates: {
          // 自訂欄位 路由規則 //
          host(h, row) {
            const hostUrlInfos = [];
            
            row.rules.forEach(rule => {
              
              const host = rule.host;

              rule.paths.forEach(path => {
                // console.log("aaaaaaaaaaaa", path);
                const completePath = host + path.path;

                const url = "http://" + completePath;
                const workloadId = path.workloadIds[0];
                var result = workloadId.split(":");
                var last = result[result.length - 1];

                hostUrlInfos.push(
                  <div>
                    <a href={url} target="_blank">
                      {completePath}
                    </a>{" "}
                    &#x2192;{last}
                  </div>
                );
              });
            });
            return <div>{hostUrlInfos}</div>;
          }
        }, 
      },     
      isLoading: false
    };
  },

  mounted(){
    this.strProject = this.$t('option.PROJECT')
    this.strNamespace = this.$t('option.NAMESPACE')
    this.$router.push({
      path:'/admin/ingress/project',
      params:{
        project:"project"
      }
    })
    this.isLoading = true;
    backendapi.get("/project").then((response) => {
      this.isLoading = false;
      this.alldata = response.data;
      this.projectlist = response.data;
    })
    .catch(error => {
       alert(error.message)
    });
  },

  watch:{
    $route (to, from){
      if(location.pathname=="/admin/ingress"){
        this.$router.push({
          path:'/admin/ingress/project',
          params:{
            project:"project"
          }
        })
      }
      if(this.$route.query.project){
        this.strProject = this.$route.query.project
      }else{
        this.strProject = this.$t('option.PROJECT')
      }
      if(this.$route.query.namespace){
        this.strNamespace = this.$route.query.namespace
        backendapi.get(`/project/${this.strProject}/namespace/${this.strNamespace}/ingress`)
          .then(response => {
            this.ip = response.data.lb_external_ip
            this.tableData = response.data.ingress.map((item, index) => {
                item['id'] = index
                return item
            });

            this.isLoading = false;
            
          })
          .catch(error => {
            console.log(error);
            this.isLoading = false;
          });
      }else{
        this.strNamespace = this.$t('option.NAMESPACE')
        this.disabled = true;
      }
    }
  },

  methods:{

    // 選取專案 //
    selectedproject() {
      var projectlist = document.getElementById("projectlist");
      this.strProject = projectlist.options[projectlist.selectedIndex].value;
      clearInterval(this.timer1);

      this.$router.push({
        path:`/admin/${this.url}/namespace`,
        query:{
          project: this.strProject
        }
      })

      this.isLoading = true;
      this.tableData = []

      backendapi.get(`/project/${this.strProject}/namespace`).then((response) => {
        this.namespacelist = response.data;        
        if (this.namespacelist.length >= 1 ){
          this.disabled = false
        }else{
          this.disabled = true
        }
        this.isLoading = false;
      })
      .catch(error => {
        alert(error.message)
        this.isLoading = false;
      });
    },
    

    // 選取命名空間 //
    selectednamespace() {
      var namespacelist = document.getElementById("namespacelist");
      this.strNamespace = namespacelist.options[namespacelist.selectedIndex].value;
      clearInterval(this.timer1);
      
      this.$router.push({
        path:'namespace',
        query:{
          project:this.$route.query.project,
          namespace: this.strNamespace
        }
      })

      this.isLoading = true;
      backendapi.get(`/project/${this.strProject}/namespace/${this.strNamespace}/ingress`).then((response) => {
        this.ip = response.data.lb_external_ip
        this.tableData = response.data.ingress.map((item, index) => {
            item['id'] = index
            return item
        });

        this.isLoading = false;
      })
      .catch(error => {
        alert(error.message)
        this.isLoading = false;
      });
    },

    // 接收專案 //
    getPjname(PJname){
      this.strProject = PJname
      this.isLoading = true;
      clearInterval(this.timer1);
      backendapi.get(`/project/${this.strProject}/namespace`).then(response => {
        this.namespacelist = response.data;
        this.isLoading = false
        if (this.namespacelist.length >= 1) {
          this.disabled = false;
        } else {
          this.disabled = true;
        }
      })
      .catch(error => {
        console.log(error);
      });  
    },


    // 接收命名空間//
    getNamespace(namespace){
      this.strNamespace = namespace
      clearInterval(this.timer1);
      this.isLoading = true;
      backendapi.get(`/project/${this.strProject}/namespace/${this.strNamespace}/ingress`).then((response) => {
        this.ip = response.data.lb_external_ip

        this.tableData = response.data.ingress.map((item, index) => {
            item['id'] = index
            return item
        });

        this.isLoading = false;
      })
      .catch(error => {
        alert(error.message)
        this.isLoading = false;
      });
    },


    // 獲取 workloadId //
    workload(){
      backendapi.get(`/project/${this.strProject}/namespace/${this.strNamespace}/service`).then((response) => {
        this.newIngress = {
          name:'',
          rewritebackend: false,
          host: '',
          paths:[
            {
              path:'/',
              targetPort: "80",
              workloadIds: [],
              selected: [
                {
                  name: ""
                }
              ]
            }
          ]
        }
        this.newIngress.host = this.newIngress.name + "." + this.ip + ".xip.io"
        this.workloadIds = response.data;
      })
      .catch(error => {
        alert(error.message)
      });
    },


    // 讀取表單 //
    reloadd(){
      backendapi.get(`/project/${this.strProject}/namespace/${this.strNamespace}/ingress`).then((response) => {
        this.workload()
        this.ip = response.data.lb_external_ip
        this.tableData = response.data.ingress.map((item, index) => {
            item['id'] = index
            return item
        });
        this.isLoading = false;
      })
    },

    // 元件呼叫讀取表單 //
    loadTable() {
      this.$nextTick(() => {
        this.$refs.modal.hide();
      });
      this.timer1 = setInterval(()=> {this.reloadd()}, 5000);
    },

    // 元件呼叫停止讀取表單 //
    stopLoadTable(){
      clearInterval(this.timer1);
    },


    // 表單編輯出現 //
    editpop (row) {
      this.strIngress = row.name
    },

    // 分類表單刪除 //
    remove (row) {
      this.isLoading = true;
      this.tableData.filter(item => (row.id !== item.id));
      this.strIngress = this.tableData[row.id].name
      backendapi.delete(`/project/${this.strProject}/namespace/${this.strNamespace}/ingress/${this.strIngress}`).then((response) => {
        this.loadTable();
      })
      .catch(error => {
        alert(error.message)
        this.isLoading = false;
      });
    },
    // 刪除步驟 //
    checkCancel(row) { 
      swal({
          title: this.$t('service.INGRS_DIAL_DELINGRES_MSG'),
          text: this.$t('common.MSG_IRREVERSIBLE_ACTION'),
          type: "warning",
          showCancelButton: true,
          confirmButtonColor: "#DD6B55",
          confirmButtonText: this.$t('common.BTN_CONFIRM'),
          cancelButtonText:this.$t('common.BTN_CANCEL'),
          heightAuto: false
      }).then((result) => {
          if(result.value){
          this.remove(row);
            swal({
                title: this.$t('common.MSG_DEL_SUCCESS'),
                confirmButtonText: this.$t('common.BTN_OK'),
                type: "success",
                heightAuto: false
            });
          } 
      });
    }
  },
  destroyed: function() {
    console.log('ingress timer destroyed');
    clearInterval(this.timer1);
  }
}
</script>

<style>
#createIngress .row{
  margin:0 15px;
}
#createIngress .switch input:checked+span {
    background-color: #18a0ca;
    border-color: #18a0ca;
}
fieldset:last-child{
  margin-bottom: 0;
}

.must_write{
  color:red;
  font-size: 13px;
  margin-left: 5px;
  margin-bottom: 5px;
}

.invalid-feedback {
  display: block;
  margin-left: 200px;
}
</style>


