import Vue from 'vue'
import axios from 'axios'
import VueAxios from 'vue-axios'
import App from './App'
import router from './router'
import currencyFilter from './filters/currency'
import Loading from 'vue-loading-overlay';
import Vuex from 'vuex';
import store from './store'
import VueI18n from 'vue-i18n'
import Configuration from './ConfigProvider'
Vue.use(VueI18n)
// Bootstrap Vue
import 'bootstrap-vue/dist/bootstrap-vue.css'
import BootstrapVue from 'bootstrap-vue'
import 'vue-loading-overlay/dist/vue-loading.css';
import '@fortawesome/fontawesome-free/css/brands.css';
import '@fortawesome/fontawesome-free/css/regular.css';
import '@fortawesome/fontawesome-free/css/solid.css';
import '@fortawesome/fontawesome-free/css/fontawesome.css';
import VueSlimScroll from 'vue-slimscroll';
import VueObserveVisibility from 'vue-observe-visibility';

const https = require('https');

Vue.use(VueSlimScroll)
Vue.use(VueAxios, axios);
Vue.use(Vuex);
Vue.filter('currency', currencyFilter);
Vue.use(BootstrapVue);
Vue.use(VueObserveVisibility);
Vue.component('Loading', Loading);

// Vue.config.productionTip = false
window.moment = require('moment');

// const reversProxyUrl = Configuration.value('backendHost') || "http://10.62.164.169:8686";
const reversProxyUrl = Configuration.value('backendHost') || "https://enigmatic-river-95619.herokuapp.com";

console.log('main.js backend url', reversProxyUrl);

// prepare kibana host param
const urlObj = new URL(reversProxyUrl);

Vue.prototype.$baseHOST = urlObj.protocol + "//" + urlObj.host;
Vue.prototype.$baseProtocol = urlObj.protocol;
Vue.prototype.$baseHostNamel = urlObj.hostname;
Vue.prototype.$basePort = urlObj.port;

const backendapi = axios.create({
  "Content-Type": "application/json",
  // baseURL: "https://192.168.56.104/paas",
  baseURL: "https://enigmatic-river-95619.herokuapp.com",
  // timeout: 1000,
  headers: {
    clusterid: localStorage.getItem('clusterid'),
    Authorization: "bearer " + localStorage.getItem('token')
  },
  httpsAgent: new https.Agent({
    rejectUnauthorized: false
  })
});
window.backendapi = backendapi;
window.reversProxyUrl = reversProxyUrl;



backendapi.interceptors.response.use(
  response => {
    return response;
  },
  err => {
    if (err && err.response) {
      switch (err.response.status) {
        case 401:
          alert(this.$t('common.STATUS_CODE_401'));
          break;
        case 403:
          alert(this.$t('common.STATUS_CODE_403'))
          break;
        default:
          console.log(err.messages);
      }
    } else {
      console.log(err.messages);
    }
    return Promise.resolve(err.response);
  }
);


import tw from './locales/tw.json'; // 繁中翻譯
import en from './locales/en.json'; // 英文翻譯
import ch from './locales/ch.json'; // 簡中翻譯
const locale = 'tw'
const messages = {
  tw,
  en,
  ch
}
const i18n = new VueI18n({
  locale,
  messages
})

router.beforeEach((to, from, next) => {
  const istoken = localStorage.getItem('token');
  const isclusterid = localStorage.getItem('clusterid');
  if (istoken && isclusterid) {
    next();
  } else {
    if (to.path !== '/login')
      next('/login');
    else
      next();
  }
});

export const app = new Vue({
  el: '#app',
  router,
  i18n,
  store,
  components: {
    App
  },
  template: '<App/>',
  render: h => h(App)
}).$mount('#app')



