<template>
  <div>

    <form ref="form" @submit="handleSubmit" @change="handleChange"  @invalid.capture.prevent="handleInvalid">
      <b-card class="mb-3 border">


        <!-- 專案，命名空間 開始 -->
        <fieldset>
          <dl class="row">
            <dt class="col-sm-3">{{$t("service.CFGMAP_DIAL_NEWCFGMAP_LBL_PRJ_NAME")}}:</dt>
            <dd class="col-sm-9">{{strProject}}</dd>
          </dl>
          <dl class="row">       
            <dt class="col-sm-3">{{$t("service.CFGMAP_DIAL_NEWCFGMAP_LBL_NAMESPACE")}}:</dt>
            <dd class="col-sm-9">{{strNamespace}}</dd>
          </dl>
        </fieldset>
        <!-- 專案，命名空間 結束 -->   


        <!-- 新建環境配置名稱 描述 開始 -->
        <fieldset>
            <div class="form-group row">
                <label class="col-2 col-form-label">{{$t("service.CFGMAP_DIAL_NEWCFGMAP_CFGMAP_NAME")}}</label>
                <div class="col-10">
                  <input v-model="newConfigure.name" :class="{'form-control':true}" type="text" name="name" @blur="checkDuplicate" required/>
                  <p v-if="fieldErrors.name" class="help is-danger">{{fieldErrors.name}}</p>
                  <p v-if="duplicate" class="help is-danger">{{$t('service.CFGMAP_DIAL_NEWCFGMAP_HINT_FILENAME_EXIST')}}</p>
                </div>
            </div>
        </fieldset>
        <fieldset>
            <div class="form-group row">
                <label class="col-2 col-form-label">{{$t("service.CFGMAP_DIAL_NEWCFGMAP_CFGMAP_DESC")}}</label>
                <div class="col-10">
                  <input v-model="newConfigure.description" :class="{'form-control':true}" type="text"/>
                </div>
            </div>
        </fieldset>
        <!-- 新建環境配置名稱 描述 結束 -->

        <!-- 變數與值 開始 -->
        <fieldset>
            <div class="form-group row align-items-end" v-for="(item, index) in newdata">
              <div class="col-5">
                <div>
                  <label class="col-form-label">{{$t("service.CFGMAP_DIAL_NEWCFGMAP_IPTLBL_KEY")}}</label>
                  <input v-model="item.key" :class="{'form-control':true}" type="text" name="configKey" required/>
                </div>
                <div>
                  <p v-if="fieldErrors.configKey" class="help is-danger">{{fieldErrors.configKey}}</p>
                </div>
              </div>             
              <div class="col-1">
                <h4 class="text-center"> = </h4>
              </div>
              <div class="col-5">
                <div>
                 <label class="col-form-label">{{$t("service.CFGMAP_DIAL_NEWCFGMAP_IPTLBL_VALUE")}}</label>
                  <textarea v-model="item.value" :class="{'form-control':true}" type="text" name="configValue" required style="height:35px;"></textarea>
                </div>
                <div>
                  <p v-if="fieldErrors.configValue" class="help is-danger">{{fieldErrors.configValue}}</p>
                </div>
              </div>
              <div class="col-1">
                <div class="col-1">
                  <b-btn variant="outline-danger" class="btn-xs" @click.prevent="removeRule(index)" style="margin-bottom: 5px;"  v-b-tooltip.hover :title="$t('common.ICON_TIP_DEL')"><i class="fas fa-trash-alt"></i></b-btn>
                </div>
              </div>
            </div>
            <!-- 變數與值 結束 -->
          <br>

          <!-- 新增變數 開始 -->
          <button class="btn btn-labeled btn-info" type="button" @click="addrule()">
            <span class="btn-label"><i class="fa fa-plus"></i></span>{{$t('service.CFGMAP_DIAL_NEWCFGMAP_BTN_ADD_VAR')}}
          </button>
          <!-- 新增變數 結束 -->

        </fieldset>
      </b-card>    
    </form>

    <div class="modal-footer mt-4">
      <b-button size="sm" variant="success" @click="handleSubmit()">{{$t("service.CFGMAP_DIAL_NEWCFGMAP_BTN_SUBMIT")}}</b-button>
      <b-button size="sm" variant="danger" @click="cancel()">{{$t("service.RSRC_DIAL_DELPOOL_CANCEL")}}</b-button>
    </div>

  </div>
</template>  

<script>
import Vue from 'vue';

export default {
  props:["strProject" , "strNamespace"],
  components: {
  },

  data(){
    return{

      duplicate: false,
      fieldErrors: {},     

      newConfigure:{
        projectName:'',
        spacetName:'',
        name:'',
        description:'',
      },

      newdata:[
        {
          key:"",
          value:""
        },             
      ],

      isLoading: false,

    };
  },

  mounted(){
    this.$emit('stopLoadTable')
    this.newConfigure.name = ""
    this.newConfigure.description = ""
    this.newdata = [        
      {
        key:"",
        value:""
      },
    ]
  },

  methods:{
    // 新增變量 //
    addrule(){
      const newObject = {
        key:"",
        value: ""
      }
      this.newdata.push(newObject);
    },
    // 刪除變量 //
    removeRule (index) {
      this.newdata.map((item, index) => {
        item['id'] = index
        return item
      });  
      if (this.newdata.length <= 1) {
        return
      }else{
        var outIndex = this.newdata.filter(item => (this.newdata[index].id === item.id));
        this.newdata.splice(outIndex[0].id, 1)
      }  
    },
    handleChange(evt) {
      console.log('handleChange :: ', evt.target.name);
      this.fieldErrors = {
        ...this.fieldErrors,
        [evt.target.name]: ""
      };
    },

    handleInvalid(evt) {
      console.log('handleInvalid :: ', evt.target.name);
      this.fieldErrors = {
        ...this.fieldErrors,
        [evt.target.name]: evt.target.validationMessage
      };
    },

    errorClass(field) {
      return this.fieldErrors[field] ? "{is-danger, {'form-control':true}}" : ""
    },
    // //
    checkDuplicate() {
      if (this.newConfigure.name){
        var target = this.tableData.find(item => item.name === this.newConfigure.name);

        if(target){
          this.duplicate = true;
        }else{
          this.duplicate = false;
        }

      }else{
        console.log('empty');
      }
    },

    // 驗證 //
    checkFormValidity() {
      const valid = this.$refs.form.checkValidity()  && !this.duplicate;
      this.nameState = valid? 'valid' : 'invalid'
      return valid
    },

    // 新建配置項送出 //
    handleSubmit() {
      if (!this.checkFormValidity()) {
        return
      }
      this.go();
    },
    go(){
      var result = {};
      for (var i = 0; i < this.newdata.length; i++) {
        result[this.newdata[i].key] = this.newdata[i].value;
      }
      backendapi.post(`/project/${this.strProject}/namespace/${this.strNamespace}/configmap`,{
        name: this.newConfigure.name,
        description: this.newConfigure.description,
        data: result
      }).then((response) => {
        this.$emit('loadTable')
      })
      .catch(error => {
        alert(error.message);
        this.$emit('loadTable')
      });
    },
    cancel(){
      this.$emit('loadTable')
    },
  },

}
</script>

<style>
#createConfigure .modal-xl {
  max-width: 900px;
}

#createConfigure .help {
    display: block;
    font-size: 0.75rem;
    margin-top: 0.25rem;
}

#createConfigure .help.is-danger{
    color: #ff3860;
}
#configure  fieldset:last-child{
  margin-bottom: 0;
}
</style>


